import './styles.css';
import {useCallback, useEffect, useState} from "react";
import {requestBackend} from "../../util/requests";
import {SpringPage} from "../../types/spring";
import {AxiosRequestConfig} from "axios";
import Pagination from "../../components/Pagination";
import {Link} from 'react-router-dom';
import {VagaDTO} from "../../types/papel";
import VagasAdminFilter, {VagasAdminFilterData} from "./VagasAdminFilter";
import CardVagaAdmin from "./CardVagaAdmin";

type ControlComponentsData = {
    activePage: number;
    filterData: VagasAdminFilterData;
}

const VagasAdmin = () => {

    const [page, setPage] = useState<SpringPage<VagaDTO>>();

    const [controlComponentsData, setControlComponentsData] = useState<ControlComponentsData>(
        {
            activePage: 0,
            filterData: {titulo: "", empresa: null, tipoVaga: null}
        }
    );

    const handlePageChange = (pageNumber: number) => {
        setControlComponentsData({activePage: pageNumber, filterData: controlComponentsData.filterData});
    };

    const handleSubmitFilter = (data: VagasAdminFilterData) => {
        setControlComponentsData({activePage: 0, filterData: data});
    }

    const getVagas = useCallback(() => {
        const config: AxiosRequestConfig = {
            method: 'GET',
            url: '/vagas',
            withCredentials: true,
            params: {
                page: controlComponentsData.activePage,
                size: 3,
                titulo: controlComponentsData.filterData.titulo,
                empresaId: controlComponentsData.filterData.empresa?.id,
                tipoId: controlComponentsData.filterData.tipoVaga?.id
            }
        };

        requestBackend(config)
            .then(response => {
                    setPage(response.data);
                }
            );
    }, [controlComponentsData]);

    useEffect(() => {
        getVagas();
    }, [getVagas]);


    return (
        <div className="product-crud-container">
            <div className="product-crud-bar-container">
                <Link to="/app/vaga-admin/nova">
                    <button className="btn btn-primary text-white btn-crud-add">
                        ADICIONAR
                    </button>
                </Link>

                <VagasAdminFilter onSubmitFilter={handleSubmitFilter}/>
            </div>
            <div className="row">
                {
                    page?.content.map(vaga => (
                        <div key={vaga.id} className="col-sm-6 col-md-12">
                            <CardVagaAdmin vaga={vaga} onDelete={getVagas}/>
                        </div>
                    ))
                }
            </div>
            {
                page && page?.totalElements > 0 &&
                <Pagination forcePage={page?.number} pageCount={(page) ? page.totalPages : 0} range={3}
                            onChange={handlePageChange}/>
            }
            {
                page && page?.totalElements === 0 &&
                <h3>Nenhuma vaga encontrada.</h3>
            }
        </div>
    );
}

export default VagasAdmin;
