import {ReactComponent as ArrowIcon} from 'assets/images/arrow.svg';
import {Link, useHistory, useParams} from 'react-router-dom';
import './styles.css';
import {useEffect, useState} from "react";
import {
    requestAlterarSenha,
    requestAtivarUsuario,
    requestBackend,
    requestBackendReset,
    requestInativarUsuario
} from "../../util/requests";
import {UsuarioDTO} from "../../types/papel";
import {toast} from "react-toastify";
import {getTokenData} from "../../util/auth";
import ButtonIcon from "../../components/ButtonIcon";
import {useForm} from "react-hook-form";


type UrlParams = {
    usuarioId: string;
}

type FormData = {
    senhaNova: string;
}

const NovaSenhaAdmin = () => {

    const history = useHistory();

    const {usuarioId} = useParams<UrlParams>();

    const [usuario, setUsuario] = useState<UsuarioDTO>();

    useEffect(() => {
        requestBackend({url: `/usuarios/${usuarioId}`, withCredentials: true})
            .then(response => {
                setUsuario(response.data)
            })
    }, [usuarioId]);

    const {register, handleSubmit, formState: {errors}} = useForm<FormData>();

    const onSubmit = (formData: FormData) => {
    requestAlterarSenha(usuarioId, formData)
        .then(response => {
            toast.success(`Senha do usuário ${usuario?.email} alterada`);
                history.push(`/app/usuario/${usuarioId}`);
            })
        .catch(() => {
            toast.error("Erro ao alterar a senha");
        })
    };

    return (
        <div className="product-details-container">
            <div className="base-card product-details-card">
                <Link to={`/app/usuario/${usuarioId}`}>
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>CANCELAR</h2>
                    </div>
                </Link>
                <div className="row">

                    <div className="col-xl-6">
                        <div className="description-container">
                            <h2>Alterando a senha do usuário <br/>{usuario?.email}</h2>
                            <form onSubmit={handleSubmit(onSubmit)}>
                                <div className="mb-2">
                                    <input
                                        {...register("senhaNova", {
                                            required: 'Campo obrigatório'
                                        })}
                                        type="password"
                                        className={`form-control base-input novaSenha ${errors.senhaNova ? 'is-invalid' : ''}`}
                                        placeholder="Nova senha"
                                        name="senhaNova"
                                    />
                                    <div className="invalid-feedback d-block">{errors.senhaNova?.message}</div>
                                </div>

                                <button className="btn-outline-primary product-crud-card-button">
                                    ALTERAR
                                </button>
                            </form>
                            {/*<h2>Descrição do usuário</h2>*/}
                            {/*<p>Tipo: {papel}</p>*/}
                            {/*<p>Nome: {usuario?.nome}</p>*/}
                            {/*<p>Email: {usuario?.email}</p>*/}
                            {/*{usuario?.nomeEmpresa &&*/}
                            {/*    <p>Empresa: {usuario?.nomeEmpresa}</p>*/}
                            {/*}*/}
                            {/*{usuario?.ativo && <div className="frase">*/}
                            {/*    <p>*/}
                            {/*        Status:*/}
                            {/*        <text className="title"> Ativo</text></p>*/}
                            {/*</div>}*/}
                            {/*{!usuario?.ativo && <div className="frase">*/}
                            {/*    <p>*/}
                            {/*        Status:*/}
                            {/*        <text className="text-danger"> Inativo</text></p>*/}
                            {/*</div>}*/}
                        </div>
                    </div>

                    {/*<div className="col-xl-6">*/}
                    {/*    <a href="#" onClick={handleAtivarClick}>*/}
                    {/*        <button className="btn-outline-primary product-crud-card-button">*/}
                    {/*            SALVAR*/}
                    {/*        </button>*/}
                    {/*    </a>*/}
                    {/*    <br/>*/}
                    {/*    <Link to={`/app/usuario/${usuarioId}`}>*/}
                    {/*        <button*/}
                    {/*            className="btn-outline-danger product-crud-card-button product-crud-card-button-first">*/}
                    {/*            CANCELAR*/}
                    {/*        </button>*/}
                    {/*    </Link>*/}
                    {/*</div>*/}
                </div>
            </div>
        </div>
    );
}

export default NovaSenhaAdmin;