import {Link, Redirect, useHistory, useLocation} from 'react-router-dom';
import ButtonIcon from 'components/ButtonIcon';

import './styles.css';
import {useForm} from "react-hook-form";
import {requestBackendReset} from "../../../util/requests";
import logoIfsc from "../../../assets/images/ifsc-logo.png";
import {ReactComponent as ArrowIcon} from "../../../assets/images/arrow.svg";
import {useState} from "react";

type FormData = {
    codigoRecuperacao: string;
    senhaNova: string;
}

type Username = {
    username: string;
}

const Reset = () => {

    const history = useHistory();

    const {register, handleSubmit, formState: {errors}} = useForm<FormData>();

    const username = useLocation<Username>().state?.username;

    const [hasRecoverError, setHasRecoverError] = useState(false);

    const onSubmit = (formData: FormData) => {
        requestBackendReset(formData, username)
            .then(response => {
                history.push('/auth/reset/success', { username: username})
            })
            .catch(error => {
                if (error.response.status === 404 || error.response.status === 403 || error.response.status === 422) {
                    setHasRecoverError(true);
                }
            });
    };

    if (username !== undefined){
        return (
            <div className="base-card login-card">
                <div className="logo-mobile">
                    <img src={logoIfsc} alt="Logo do IFSC" className="img-card"/>
                    <h3>IFSC VAGAS</h3>
                    <h4>Câmpus Tubarão</h4>
                </div>

                <Link to="/auth">
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>CANCELAR</h2>
                    </div>
                </Link>

                <h1>RECUPERAR SENHA</h1>

                <p>Código enviado para:</p>
                <p>{username}</p>

                <form onSubmit={handleSubmit(onSubmit)}>
                    <div className="mb-4">
                        <input
                            {...register("codigoRecuperacao", {
                                required: 'Campo obrigatório'
                            })}
                            type="text"
                            className={`form-control base-input ${errors.codigoRecuperacao ? 'is-invalid' : ''}`}
                            placeholder="Código de recuperação"
                            name="codigoRecuperacao"
                        />
                        <div className="invalid-feedback d-block">{errors.codigoRecuperacao?.message}</div>
                    </div>
                    <div className="mb-2">
                        <input
                            {...register("senhaNova", {
                                required: 'Campo obrigatório'
                            })}
                            type="password"
                            className={`form-control base-input ${errors.senhaNova ? 'is-invalid' : ''}`}
                            placeholder="Nova senha"
                            name="senhaNova"
                        />
                        <div className="invalid-feedback d-block">{errors.senhaNova?.message}</div>
                    </div>

                    { hasRecoverError &&
                    <div className="auth-error">
                        <p>Código inválido, tente novamente.</p>
                    </div>
                    }

                    <div className="login-submit">
                        <ButtonIcon text="Alterar senha"/>
                    </div>
                </form>
            </div>
        );
    } else {
        return (
            <div className="base-card login-card">
                <div className="logo-mobile">
                    <img src={logoIfsc} alt="Logo do IFSC" className="img-card"/>
                    <h3>IFSC VAGAS</h3>
                    <h4>Câmpus Tubarão</h4>
                </div>

                <Link to="/auth">
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR PARA LOGIN</h2>
                    </div>
                </Link>

                <h1>RECUPERAR SENHA</h1>
                <h4 className="auth-error">Houve um problema ao identificar o usuário.</h4>
            </div>
        );
    }

};

export default Reset;
