import {Link, useLocation} from 'react-router-dom';

import './styles.css';
import logoIfsc from "../../../assets/images/ifsc-logo.png";
import {ReactComponent as ArrowIcon} from "../../../assets/images/arrow.svg";

type Email = {
    email: string;
}

const RegisterSuccess = () => {

    const email = useLocation<Email>().state?.email;

    if (email !== undefined) {
        return (
            <div className="base-card login-card">
                <div className="logo-mobile">
                    <img src={logoIfsc} alt="Logo do IFSC" className="img-card"/>
                    <h3>IFSC VAGAS</h3>
                    <h4>Câmpus Tubarão</h4>
                </div>

                <Link to="/auth">
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR PARA LOGIN</h2>
                    </div>
                </Link>

                <h3>Cadastro solicitado com sucesso!</h3><br/>
                <h4>Você receberá um e-mail em <u>{email}</u> quando o administrador da instituição der um feedback para sua solicitação.</h4>
            </div>
        );
    } else {
        return (
            <div className="base-card login-card">
                <div className="logo-mobile">
                    <img src={logoIfsc} alt="Logo do IFSC" className="img-card"/>
                    <h3>IFSC VAGAS</h3>
                    <h4>Câmpus Tubarão</h4>
                </div>

                <Link to="/auth">
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR PARA LOGIN</h2>
                    </div>
                </Link>

                <h4 className="auth-error">Houve um problema ao identificar a solicitação.</h4>
            </div>
        );
    }

};

export default RegisterSuccess;
