import './styles.css';
import 'bootstrap/js/src/collapse.js';
import {useContext, useEffect} from "react";
import {AuthContext} from "../../AuthContext";
import {getTokenData, hasAnyRoles, isAuthenticated} from "../../util/auth";
import {removeAuthData} from "../../util/storage";
import history from "../../util/history";
import {Link, NavLink} from "react-router-dom";

const Navbar = () => {

    const {authContextData, setAuthContextData} = useContext(AuthContext);

    useEffect(() => {
        if (isAuthenticated()) {
            setAuthContextData({
                authenticated: isAuthenticated(),
                tokenData: getTokenData()
            });
        } else {
            setAuthContextData({
                authenticated: false
            });
        }
    }, [setAuthContextData]);

    const handleLogoutClick = (event: React.MouseEvent<HTMLAnchorElement>) => {
        event.preventDefault();
        removeAuthData();

        setAuthContextData({
            authenticated: false
        });

        history.replace('/');
    }

    return (
        <nav className="navbar navbar-expand-xl bg-primary main-nav navbar-dark">
            <div className="container-fluid">
                <Link to="/" className="nav-logo-text">
                    <h4>IFSC Vagas</h4>
                </Link>

                <button
                    className="navbar-toggler"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#ifsc-vagas-navbar"
                    aria-controls="ifsc-vagas-navbar"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                >
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="ifsc-vagas-navbar">
                    <ul className="navbar-nav offset-md-2 main-menu">
                        {hasAnyRoles(["ROLE_ADMIN"]) &&
                            <>
                                <li className="show-only-mobile">
                                    <NavLink to="/app/usuarios" activeClassName="active">Usuários</NavLink>
                                </li>
                                <li className="show-only-mobile">
                                    <NavLink to="/app/solicitacoes" activeClassName="active">Solicitações</NavLink>
                                </li>
                                <li className="show-only-mobile">
                                    <NavLink to="/app/vagas-admin" activeClassName="active">Vagas</NavLink>
                                </li>
                            </>
                        }

                        {hasAnyRoles(["ROLE_ESTUDANTE"]) &&
                            <li className="show-only-mobile">
                                <NavLink to="/app/vagas" activeClassName="active">Visualizar vagas</NavLink>
                            </li>
                        }

                        { hasAnyRoles(["ROLE_EMPRESA"]) &&
                            <>
                                <li className="show-only-mobile">
                                    <NavLink to="/app/vagas-empresa" activeClassName="active">Minhas vagas</NavLink>
                                </li>
                                <li className="show-only-mobile">
                                    <NavLink to="/app/candidaturas" activeClassName="active">Candidaturas</NavLink>
                                </li>
                            </>
                        }
                        {
                            authContextData.authenticated &&
                            <li className="show-only-mobile">
                                <NavLink to="/app/alterar-senha" activeClassName="active">Alterar senha</NavLink>
                            </li>
                        }
                        {
                            authContextData.authenticated &&
                            <li className="show-only-mobile">
                                <NavLink to="/app/enviar-feedback" activeClassName="active">Enviar feedback</NavLink>
                            </li>
                        }
                        {
                            authContextData.authenticated &&
                            <div className="show-only-mobile logoff">
                                <a href="#logout" onClick={handleLogoutClick} className="logout">
                                    LOGOUT
                                </a>
                            </div>
                        }
                    {/*    <li>*/}
                    {/*        <NavLink to="/products" activeClassName="active">VAGAS</NavLink>*/}
                    {/*    </li>*/}
                    {/*    <li>*/}
                    {/*        <NavLink to="/admin" activeClassName="active">ALUNOS</NavLink>*/}
                    {/*    </li>*/}
                    </ul>
                </div>
                <div className="nav-login-logout">
                    {
                        authContextData.authenticated &&
                            <>
                                <span className="nav-username">{authContextData.tokenData?.user_name}</span>
                                <a href="#logout" onClick={handleLogoutClick} className="logout">
                                    LOGOUT
                                </a>
                            </>
                    }
                </div>
            </div>
        </nav>
    )
}

export default Navbar;