import './styles.css';
import {useCallback, useContext, useEffect, useState} from "react";
import {requestBackend, requestBackendPendentes} from "../../util/requests";
import {RequisicaoCadastroDTO} from "../../types/RequisicaoCadastroDTO";
import CardSolicitacaoAprovar from "./CardSolicitacaoAprovar";
import {SpringPage} from "../../types/spring";
import {AxiosRequestConfig} from "axios";
import Pagination from "../../components/Pagination";
import {SolicitacoesPendentesContext} from "../../SolicitacoesPendentesContext";

type ControlComponentsData = {
    activePage: number;
}

const Solicitacoes = () => {

    const [page, setPage] = useState<SpringPage<RequisicaoCadastroDTO>>();

    const {solicitacoesPendentes, setSolicitacoesPendentes} = useContext(SolicitacoesPendentesContext);

    const [controlComponentsData, setControlComponentsData] = useState<ControlComponentsData>(
        {
            activePage: 0
        }
    );

    const handlePageChange = (pageNumber: number) => {
        setControlComponentsData({activePage: pageNumber});
    };

    const getPendencias = useCallback(() => {
        const config: AxiosRequestConfig = {
            method: 'GET',
            url: '/usuarios/solicitacao-cadastro/pendentes',
            withCredentials: true,
            params: {
                page: controlComponentsData.activePage,
                size: 3
            }
        };

        requestBackend(config)
            .then(response => {
                    setPage(response.data);
                }
            );

        requestBackendPendentes()
            .then(response => {
                    setSolicitacoesPendentes(response.data);
                }
            );
    }, [controlComponentsData]);

    useEffect(() => {
        getPendencias();
    }, [getPendencias]);


    return (
        <div className="product-crud-container">
            <div className="row">
                {
                    page?.content.map(solic => (
                        <div key={solic.id} className="col-sm-6 col-md-12">
                            <CardSolicitacaoAprovar solicitacao={solic} onFeedback={getPendencias}/>
                        </div>
                    ))
                }
            </div>
            {
                page && page?.totalElements > 0 &&
                <Pagination forcePage={page?.number} pageCount={(page) ? page.totalPages : 0} range={3}
                            onChange={handlePageChange}/>
            }
            {
                page && page?.totalElements === 0 &&
                <h3>Não há solicitações de cadastro pendentes no momento.</h3>
            }
        </div>
    );
}

export default Solicitacoes;
