import './styles.css';
import {Switch} from "react-router-dom";
import PrivateRoute from "../../components/PrivateRoute";
import Solicitacoes from "../Solicitacoes";
import Apresentacao from "./Apresentacao";
import SideNavbar from "../../components/SideNavbar";
import Usuarios from "../Usuarios";
import DetalhesUsuario from "../DetalhesUsuario";
import NovaSenhaAdmin from "../NovaSenhaAdmin";
import NovoUsuario from "../Usuarios/NovoUsuario";
import NovoAdmin from "../Usuarios/NovoAdmin";
import NovoEmpresa from "../Usuarios/NovoEmpresa";
import NovoEstudante from "../Usuarios/NovoEstudante";
import CadastroSucesso from "../Usuarios/CadastroSucesso";
import VagasAdmin from "../VagasAdmin";
import NovaVagaAdmin from "../VagasAdmin/NovaVagaAdmin";
import RedefinirSenha from "../RedefinirSenha";
import EnviarFeedback from "../EnviarFeedback";
import VagasEmpresa from "../VagasEmpresa";
import NovaVagaEmpresa from "../VagasEmpresa/NovaVagaEmpresa";
import VagasEstudante from "../VagasEstudante";
import DetalhesVagaEstudante from "../VagasEstudante/DetalhesVagaEstudante";
import CandidatarVagaEstudante from "../VagasEstudante/CandidatarVagaEstudante";
import SucessoCandidaturaEstudante from "../VagasEstudante/SucessoCandidaturaEstudante";
import CandidaturasEmpresa from "../CandidaturasEmpresa";
import DetalhesCandidaturaEmpresa from "../CandidaturasEmpresa/DetalhesCandidaturaEmpresa";
import CandidaturasAdmin from "../CandidaturasAdmin";
import DetalhesCandidaturaAdmin from "../CandidaturasAdmin/DetalhesCandidaturaAdmin";

const Home = () => {
    return (
        <div className="admin-container">
            <SideNavbar/>
            <div className="admin-content">
                <Switch>
                    <PrivateRoute path="/app/home">
                        <Apresentacao/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/solicitacoes" roles={['ROLE_ADMIN']}>
                        <Solicitacoes/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/usuarios" roles={['ROLE_ADMIN']}>
                        <Usuarios/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/usuario/:usuarioId" roles={['ROLE_ADMIN']}>
                        <DetalhesUsuario/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/nova-senha/:usuarioId" roles={['ROLE_ADMIN']}>
                        <NovaSenhaAdmin/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/novo-usuario" roles={['ROLE_ADMIN']}>
                        <NovoUsuario/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/novo-admin" roles={['ROLE_ADMIN']}>
                        <NovoAdmin/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/novo-empresa" roles={['ROLE_ADMIN']}>
                        <NovoEmpresa/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/novo-estudante" roles={['ROLE_ADMIN']}>
                        <NovoEstudante/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/cadastro-sucesso" roles={['ROLE_ADMIN']}>
                        <CadastroSucesso/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/vagas-admin" roles={['ROLE_ADMIN']}>
                        <VagasAdmin/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/vaga-admin/:vagaId" roles={['ROLE_ADMIN']}>
                        <NovaVagaAdmin/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/alterar-senha">
                        <RedefinirSenha/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/enviar-feedback">
                        <EnviarFeedback/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/vagas-empresa" roles={['ROLE_EMPRESA']}>
                        <VagasEmpresa/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/vaga-empresa/:vagaId" roles={['ROLE_EMPRESA']}>
                        <NovaVagaEmpresa/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/vagas" roles={['ROLE_ESTUDANTE']}>
                        <VagasEstudante/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/vaga/:vagaId" roles={['ROLE_ESTUDANTE']}>
                        <DetalhesVagaEstudante/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/candidatar/:vagaId" roles={['ROLE_ESTUDANTE']}>
                        <CandidatarVagaEstudante/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/sucesso-candidatura" roles={['ROLE_ESTUDANTE']}>
                        <SucessoCandidaturaEstudante/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/candidaturas" roles={['ROLE_EMPRESA']}>
                        <CandidaturasEmpresa/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/candidatura/:candidaturaId" roles={['ROLE_EMPRESA']}>
                        <DetalhesCandidaturaEmpresa/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/candidaturas-admin" roles={['ROLE_ADMIN']}>
                        <CandidaturasAdmin/>
                    </PrivateRoute>
                    <PrivateRoute path="/app/candidatura-admin/:candidaturaId" roles={['ROLE_ADMIN']}>
                        <DetalhesCandidaturaAdmin/>
                    </PrivateRoute>
                </Switch>
            </div>
        </div>
    );
}

export default Home;
