import {ReactComponent as ArrowIcon} from 'assets/images/arrow.svg';
import {Link, useHistory, useParams} from 'react-router-dom';
import './styles.css';
import {useEffect, useState} from "react";
import {toast} from "react-toastify";
import {VagaDTO} from "../../../types/papel";
import {requestBackend} from "../../../util/requests";
import {Editor} from "react-draft-wysiwyg";
import {EditorState} from "draft-js";
import {stateFromHTML} from "draft-js-import-html";
import {formatPrice} from "../CardVagaEstudante";
import CategoryBadge from "../../Solicitacoes/CategoryBadge";
import ButtonIcon from "../../../components/ButtonIcon";
import {AxiosRequestConfig} from "axios";


type UrlParams = {
    vagaId: string;
}

const DetalhesVagaEstudante = () => {

    const history = useHistory();

    const {vagaId} = useParams<UrlParams>();

    const [vaga, setVaga] = useState<VagaDTO>();

    const [editorState, setEditorState] = useState(EditorState.createEmpty());
    const onEditorStateChange = (editorState: EditorState) => {
        setEditorState(editorState);
    };

    // const [papel, setpapel] = useState<string>();

    useEffect(() => {
        requestBackend({url: `/vagas/${vagaId}`, withCredentials: true})
            .then(response => {
                setVaga(response.data)
                const contentState = stateFromHTML(response.data.descricao || '');
                const descricaoAsEditorState = EditorState.createWithContent(contentState);
                setEditorState(descricaoAsEditorState);
            })
    }, [vagaId]);

    const handleRemoverCandidatura = () => {

        if (!window.confirm("Tem certeza que deseja remover a sua candidatura dessa vaga?")) {
            return;
        }

        const config: AxiosRequestConfig = {
            method: 'DELETE',
            url: `/candidaturas/vaga/${vagaId}`,
            withCredentials: true
        };

        requestBackend(config).then(() => {
            toast.warn(`Sua candidatura foi removida`);
            history.push(`/app/vagas`)
            history.push(`/app/vaga/${vagaId}`)
        })

    }

    return (
        <div className="product-details-container">

            <div className="base-card product-details-card">
                <Link to="/app/vagas">
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR</h2>
                    </div>
                </Link>
                <div className="row">
                    {
                        vaga?.descricao && vaga.descricao.length > 8 &&
                        <div className="col-xl-6">
                            {/*<div className="description-container">*/}
                            {/*    /!*<p>*!/*/}
                            {/*    /!*    {vaga?.descricao}*!/*/}
                            {/*    /!*</p>*!/*/}

                            <Editor
                                editorState={editorState}
                                editorClassName="editor-container  description-container description-container-vaga base-input h-auto"
                                toolbarHidden
                                readOnly
                            />

                            {/*</div>*/}
                        </div>
                    }

                    <div className="col-xl-6">
                        <div className="description-container">
                            <h2>{vaga?.titulo}</h2>
                            <p>Empresa: {vaga?.empresa.nome}</p>
                            {vaga?.localDeTrabalho && <p>Local de trabalho: {vaga.localDeTrabalho}</p>}
                            {vaga?.remuneracao && vaga?.remuneracao > 0 ?
                                <p>Remuneração: R$ {formatPrice(vaga.remuneracao)}</p> : null}
                            <div className="product-crud-categories-container">
                                {
                                    vaga?.tipos.map(tipo => (
                                        <CategoryBadge name={tipo.descricao} key={tipo.id}/>
                                    ))
                                }
                            </div>
                        </div>
                        <div className="botao-candidatar">
                            {
                                !vaga?.estudanteJaCandidatado &&
                                <Link to={`/app/candidatar/${vagaId}`}>
                                    <ButtonIcon text="Candidate-se"/>
                                </Link>
                            }
                            {
                                vaga?.estudanteJaCandidatado &&
                                <div className="diminuir">
                                    <p>
                                        Sua candidatura já foi realizada para essa vaga.
                                        <a href="#" onClick={handleRemoverCandidatura}>
                                            <button
                                                className="btn-outline-danger product-crud-card-button product-crud-card-button-first">
                                                REMOVER
                                            </button>
                                        </a>
                                    </p>
                                </div>

                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default DetalhesVagaEstudante;