import {Link, useHistory, useLocation} from 'react-router-dom';
import ButtonIcon from 'components/ButtonIcon';

import './styles.css';
import {useForm} from "react-hook-form";
import {useContext, useState} from "react";
import {AuthContext} from "../../../AuthContext";
import {
    requestBackendLogin,
    requestBackendPendentes,
    requestBackendVisualizacoesCandidaturasPendentes
} from "../../../util/requests";
import {saveAuthData} from "../../../util/storage";
import {getTokenData, hasAnyRoles, isAuthenticated} from "../../../util/auth";
import logoIfsc from "../../../assets/images/ifsc-logo.png";
import {SolicitacoesPendentesContext} from "../../../SolicitacoesPendentesContext";
import {VisualizacaoCandidaturasPendentesContext} from "../../../VisualizacaoCandidaturasPendentesContext";

type FormData = {
    username: string;
    password: string;
}

type LocationState = {
    from: string;
}

const Login = () => {

    const location = useLocation<LocationState>();

    const {from} = location.state || {from: {pathname: '/'}};

    const {setAuthContextData} = useContext(AuthContext);

    const {register, handleSubmit, formState: {errors}} = useForm<FormData>();

    const history = useHistory();

    const [hasAuthError, setHasAuthError] = useState(false);

    const {solicitacoesPendentes, setSolicitacoesPendentes} = useContext(SolicitacoesPendentesContext);
    const {visualizacaoCandidaturasPendentes, setVisualizacaoCandidaturasPendentes} = useContext(VisualizacaoCandidaturasPendentesContext);

    const onSubmit = (formData: FormData) => {
        requestBackendLogin(formData)
            .then(response => {
                saveAuthData(response.data);

                setAuthContextData({
                    authenticated: isAuthenticated(),
                    tokenData: getTokenData()
                });

                if (hasAnyRoles(['ROLE_ADMIN'])) {
                    requestBackendPendentes()
                        .then(response => {
                                setSolicitacoesPendentes(response.data);
                            }
                        );
                }

                if (hasAnyRoles(['ROLE_EMPRESA'])) {
                    requestBackendVisualizacoesCandidaturasPendentes()
                        .then(response => {
                                setVisualizacaoCandidaturasPendentes(response.data);
                            }
                        );
                }

                history.replace(from);
            })
            .catch(error => {
                if (error.response.status === 400) {
                    setHasAuthError(true);
                }
            });
    };

    return (
        <div className="base-card login-card">
            <div className="logo-mobile">
                <img src={logoIfsc} alt="Logo do IFSC" className="img-card"/>
                <h3>IFSC VAGAS</h3>
                <h4>Câmpus Tubarão</h4>
            </div>
            <h1>LOGIN</h1>
            {hasAuthError &&
            <div className="auth-error">
                <p>E-mail ou senha incorretos, tente novamente.</p>
            </div>
            }
            <form onSubmit={handleSubmit(onSubmit)}>
                <div className="mb-4">
                    <input
                        {...register("username", {
                            required: 'Campo obrigatório',
                            pattern: {
                                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                message: 'E-mail inválido'
                            }
                        })}
                        type="text"
                        className={`form-control base-input ${errors.username ? 'is-invalid' : ''}`}
                        placeholder="E-mail"
                        name="username"
                    />
                    <div className="invalid-feedback d-block">{errors.username?.message}</div>
                </div>
                <div className="mb-2">
                    <input
                        {...register("password", {
                            required: 'Campo obrigatório'
                        })}
                        type="password"
                        className={`form-control base-input ${errors.password ? 'is-invalid' : ''}`}
                        placeholder="Senha"
                        name="password"
                    />
                    <div className="invalid-feedback d-block">{errors.password?.message}</div>
                </div>
                <Link to="/auth/recover" className="login-link-recover">
                    Esqueci a senha
                </Link>
                <div className="login-submit">
                    <ButtonIcon text="Fazer login"/>
                </div>
                <div className="signup-container">
                    <span className="not-registered">Ainda não possui cadastro?</span>
                    <Link to="/auth/register" className="login-link-register">
                        SOLICITE AQUI
                    </Link>
                </div>
            </form>
        </div>
    );
};

export default Login;
