import {Link, useHistory} from 'react-router-dom';
import ButtonIcon from 'components/ButtonIcon';

import './styles.css';
import {useForm} from "react-hook-form";
import {requestBackendCompanyRegister} from "../../../util/requests";
import logoIfsc from "../../../assets/images/ifsc-logo.png";
import {ReactComponent as ArrowIcon} from "../../../assets/images/arrow.svg";
import {useState} from "react";

type FormData = {
    nomeUsuario: string;
    email: string;
    nomeEmpresa: string;
    cnpj: string;
    telefone: string;
}

const cnpjMask = (value: any) => {
    return value
        .replace(/\D+/g, '') // não deixa ser digitado nenhuma letra
        .replace(/(\d{2})(\d)/, '$1.$2') // captura 2 grupos de número o primeiro com 2 digitos e o segundo de com 3 digitos, apos capturar o primeiro grupo ele adiciona um ponto antes do segundo grupo de número
        .replace(/(\d{3})(\d)/, '$1.$2')
        .replace(/(\d{3})(\d)/, '$1/$2') // captura 2 grupos de número o primeiro e o segundo com 3 digitos, separados por /
        .replace(/(\d{4})(\d)/, '$1-$2')
        .replace(/(-\d{2})\d+?$/, '$1') // captura os dois últimos 2 números, com um - antes dos dois números
}

const CompanyRequest = () => {

    const [values, setValues] = useState({cnpj: ''})

    const inputChange = (e: any) => {
        const {name, value} = e.target
        setValues({
            ...values,
            [name]: value
        })
    }

    const history = useHistory();

    const {register, handleSubmit, formState: {errors}} = useForm<FormData>();

    const [hasRequestError, setHasRequestError] = useState(false);

    const onSubmit = (formData: FormData) => {
        requestBackendCompanyRegister(formData)
            .then(response => {
                history.push('/auth/register/success', {email: formData.email})
            })
            .catch(error => {
                if (error.response.status === 409) {
                    setHasRequestError(true);
                }
            });
    };

    const handleKeyPress = () => {
        setHasRequestError(false)
    }

    return (
        <div className="base-card login-card">
            <div className="logo-mobile">
                <img src={logoIfsc} alt="Logo do IFSC" className="img-card"/>
                <h3>IFSC VAGAS</h3>
                <h4>Câmpus Tubarão</h4>
            </div>

            <Link to="/auth/register">
                <div className="goback-container">
                    <ArrowIcon/>
                    <h2>VOLTAR</h2>
                </div>
            </Link>

            <h1>SOLICITAR CADASTRO DE EMPRESA</h1>

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className="mb-4">
                    <input
                        {...register("nomeEmpresa", {
                            required: 'Campo obrigatório'
                        })}
                        type="text"
                        className={`form-control base-input ${errors.nomeEmpresa ? 'is-invalid' : ''}`}
                        placeholder="Nome da empresa"
                        name="nomeEmpresa"
                    />
                    <div className="invalid-feedback d-block">{errors.nomeEmpresa?.message}</div>
                </div>
                <div className="mb-4">
                    <input
                        {...register("email", {
                            required: 'Campo obrigatório',
                            pattern: {
                                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                message: 'E-mail inválido'
                            }
                        })}
                        type="text"
                        className={`form-control base-input ${errors.email ? 'is-invalid' : ''}`}
                        placeholder="E-mail"
                        name="email"
                        onChange={handleKeyPress}
                    />
                    <div className="invalid-feedback d-block">{errors.email?.message}</div>
                </div>

                <div className="mb-4">
                    <input
                        {...register("cnpj", {
                            required: 'Campo obrigatório',
                            pattern: {
                                value: /^\d{2}\.\d{3}\.\d{3}\/\d{4}\-\d{2}$/,
                                // https://www.youtube.com/watch?v=hWukAkEzBB4&ab_channel=ViniciusDacal TODO: implementar
                                message: 'CNPJ inválido'
                            }
                        })}
                        type="text"
                        className={`form-control base-input ${errors.cnpj ? 'is-invalid' : ''}`}
                        placeholder="CNPJ"
                        name="cnpj"
                        value={cnpjMask(values.cnpj)}
                        onChange={inputChange}
                    />
                    <div className="invalid-feedback d-block">{errors.cnpj?.message}</div>
                </div>

                <div className="mb-4">
                    <input
                        {...register("telefone")}
                        type="text"
                        // https://gist.github.com/vinicius-stutz/6caf02d4936069c69513 TODO: implementar
                        className={`form-control base-input ${errors.telefone ? 'is-invalid' : ''}`}
                        placeholder="Telefone ou celular para contato"
                        name="telefone"
                    />
                    <div className="invalid-feedback d-block">{errors.telefone?.message}</div>
                </div>

                <div className="mb-4">
                    <input
                        {...register("nomeUsuario", {
                            required: 'Campo obrigatório'
                        })}
                        type="text"
                        className={`form-control base-input ${errors.nomeUsuario ? 'is-invalid' : ''}`}
                        placeholder="Nome de quem utilizará o sistema"
                        name="nomeUsuario"
                    />
                    <div className="invalid-feedback d-block">{errors.nomeUsuario?.message}</div>
                </div>

                {hasRequestError &&
                <div className="auth-error">
                    <p>E-mail já registrado ou solicitação de cadastro já enviada, por favor contate o administrador.</p>
                </div>
                }

                <div className="login-submit">
                    <ButtonIcon text="Enviar"/>
                </div>
            </form>
        </div>
    );


};

export default CompanyRequest;
