import './styles.css';
import VagaImg from '../../../assets/images/vaga-icon.png';
import CompanyImg from '../../../assets/images/company-icon.png';
import AdminImg from '../../../assets/images/admin-icon.png';
import CategoryBadge from "../../Solicitacoes/CategoryBadge";
import {UsuarioDTO, VagaDTO} from "../../../types/papel";
import {useEffect, useState} from "react";
import {Link, useHistory} from 'react-router-dom';
import {AxiosRequestConfig} from "axios";
import {requestBackend} from "../../../util/requests";
import {toast} from "react-toastify";

type Props = {
    vaga: VagaDTO;
    onDelete: Function;
}

export const formatPrice = (price: number) => {
    const params = {minimumFractionDigits: 2, maximumFractionDigits: 2};

    return new Intl.NumberFormat('pt-BR', params).format(price);
}

const CardVagaEmpresa = ({vaga, onDelete}: Props) => {

    const history = useHistory();

    const handleDelete = (vagaId: number) => {

        if (!window.confirm("Tem certeza que deseja remover a vaga e todas as suas candidaturas?")) {
            return;
        }

        const config: AxiosRequestConfig = {
            method: 'DELETE',
            url: `/vagas/${vagaId}`,
            withCredentials: true
        };

        requestBackend(config).then(() => {
            toast.warn(`Vaga removida`);
            onDelete();
        })

    }

    const handleRedirectCandidaturas = (vagaId: number) => {
        history.push('/app/candidaturas', {vagaId: vagaId})
    }

    const [papel, setpapel] = useState<string>();

    return (
        <div className="base-card product-crud-card">
            <div className="product-crud-card-top-container">
                <img src={VagaImg} alt="Vaga"/>
            </div>
            <div className="product-crud-card-description">
                <div className="product-crud-card-bottom-container">
                    <div className="frase">
                        <h6>
                            <text className="title">Título: </text>
                            {vaga.titulo}</h6>
                    </div>
                    {
                        vaga.localDeTrabalho &&
                        <div className="frase">
                            <h6>
                                <text className="title">Local de trabalho: </text>
                                {vaga.localDeTrabalho}</h6>
                        </div>
                    }
                    {
                        vaga.remuneracao > 0 ?
                        <div className="frase">
                            <h6>
                                <text className="title">Remuneração: </text>
                                R$ {formatPrice(vaga.remuneracao)}</h6>
                        </div>
                            :
                            null
                    }
                    <div className="frase">
                        <h6>
                            <text className="title">Candidaturas: </text>
                            {vaga.quantidadeCandidaturas}</h6>
                    </div>
                </div>
                <div className="product-crud-categories-container">
                    {
                        vaga.tipos.map(tipo => (
                            <CategoryBadge name={tipo.descricao} key={tipo.id}/>
                        ))
                    }
                </div>
            </div>
            <div className="product-crud-card-buttons-container">
                <Link to={`/app/vaga-empresa/${vaga.id}`}>
                    <button className="btn-outline-primary product-crud-card-button product-crud-card-button-first">
                        EDITAR
                    </button>
                </Link>
                <button
                    onClick={() => handleDelete(vaga.id)}
                    className="btn-outline-danger product-crud-card-button product-crud-card-button-first">
                    EXCLUIR
                </button>
                <button
                    onClick={() => handleRedirectCandidaturas(vaga.id)}
                    className="btn-outline-secondary product-crud-card-button">
                    CANDIDATURAS
                </button>
            </div>
        </div>
    );
}

export default CardVagaEmpresa;