import {ReactComponent as AuthImage} from 'assets/images/auth-image.svg';
import {Route, Switch} from "react-router-dom";
import logoIfsc from '../../assets/images/ifsc-logo.png';
import './styles.css';
import Login from "./Login";
import Recover from "./Recover";
import Reset from "./Reset";
import ResetSuccess from "./ResetSuccess";
import Register from "./Register";
import Request from "./Request";
import StudentRequest from "./StudentRequest";
import RegisterSuccess from "./RegisterSuccess";
import CompanyRequest from "./CompanyRequest";

const Auth = () => {
    return (
        <div className="auth-container">
            <div className="auth-banner-container">
                <img src={logoIfsc} alt="Logo do IFSC"/>
                <h1>IFSC Vagas</h1>
                <p>Câmpus Tubarão</p>
                <AuthImage/>
            </div>
            <div className="auth-form-container">
                <Switch>
                    <Route path="/auth/login">
                        <Login/>
                    </Route>
                    <Route path="/auth/register" exact>
                        <Register/>
                    </Route>
                    <Route path="/auth/register/student">
                        <StudentRequest/>
                    </Route>
                    <Route path="/auth/register/company">
                        <CompanyRequest/>
                    </Route>
                    <Route path="/auth/register/success">
                        <RegisterSuccess/>
                    </Route>
                    <Route path="/auth/recover">
                        <Recover/>
                    </Route>
                    <Route path="/auth/reset" exact>
                        <Reset />
                    </Route>
                    <Route path="/auth/reset/success">
                        <ResetSuccess />
                    </Route>
                </Switch>
            </div>
        </div>
    );
}

export default Auth;