import {ReactComponent as ArrowIcon} from 'assets/images/arrow.svg';
import {Link, useHistory} from 'react-router-dom';
import ButtonIcon from 'components/ButtonIcon';

import './styles.css';
import {useForm} from "react-hook-form";
import {useState} from "react";
import {requestBackendRecover} from "../../../util/requests";
import logoIfsc from "../../../assets/images/ifsc-logo.png";

type FormData = {
    username: string;
}

const Recover = () => {

    const {register, handleSubmit, formState: {errors}} = useForm<FormData>();

    const history = useHistory();

    const [hasAuthError, setHasAuthError] = useState(false);

    const onSubmit = (formData: FormData) => {
        requestBackendRecover(formData.username)
            .then(response => {
                history.push('/auth/reset', {username: formData.username})
            })
            .catch(error => {
                if (error.response.status === 404) {
                    setHasAuthError(true);
                }
            });
    };

    return (
        <div className="base-card login-card">
            <div className="logo-mobile">
                <img src={logoIfsc} alt="Logo do IFSC" className="img-card"/>
                <h3>IFSC VAGAS</h3>
                <h4>Câmpus Tubarão</h4>
            </div>
            <Link to="/auth">
                <div className="goback-container">
                    <ArrowIcon/>
                    <h2>VOLTAR</h2>
                </div>
            </Link>
            <h1>RECUPERAR SENHA</h1>
            {hasAuthError &&
            <div className="auth-error">
                <p>Usuário não encontrado no sistema, tente novamente.</p>
            </div>
            }
            <form onSubmit={handleSubmit(onSubmit)}>
                <div className="mb-4">
                    <input
                        {...register("username", {
                            required: 'Campo obrigatório',
                            pattern: {
                                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                message: 'E-mail inválido'
                            }
                        })}
                        type="text"
                        className={`form-control base-input ${errors.username ? 'is-invalid' : ''}`}
                        placeholder="E-mail"
                        name="username"
                    />
                    <div className="invalid-feedback d-block">{errors.username?.message}</div>
                </div>
                <div className="login-submit">
                    <ButtonIcon text="Avançar"/>
                </div>
            </form>
        </div>
    );
};

export default Recover;
