import './styles.css';
import {ReactComponent as SearchIcon} from "assets/images/search-icon.svg";
import {Controller, useForm} from "react-hook-form";
import Select from "react-select";
import {useEffect, useState} from "react";
import {Papel} from "../../../types/papel";
import {requestBackend} from "../../../util/requests";

export type UserFilterData = {
    email: string;
    papel: Papel | null;
}

type Props = {
    onSubmitFilter: (data: UserFilterData) => void;
}

const UserFilter = ({onSubmitFilter}: Props) => {

    const [selectPapeis, setSelectPapeis] = useState<Papel[]>([]);

    const {
        register,
        handleSubmit,
        control,
        setValue,
        getValues
    } = useForm<UserFilterData>();

    const onSubmit = (formData: UserFilterData) => {
        onSubmitFilter(formData);
    }

    const handleFormClear = () => {
        setValue('email', '');
        setValue('papel', null);
    }

    const handleChangeCategory = (value: Papel) => {
        setValue('papel', value);

        const obj: UserFilterData = {
            email: getValues('email'),
            papel: getValues('papel')
        };

        onSubmitFilter(obj);
    }

    useEffect(() => {
        requestBackend({url: '/papeis', withCredentials: true})
            .then(response => {
                setSelectPapeis(response.data.content)
            })
    }, [])

    return (
        <div className="base-card product-filter-container">
            <form onSubmit={handleSubmit(onSubmit)} className="product-filter-form">
                <div className="product-filter-name-container">
                    <input
                        {...register("email")}
                        type="text"
                        className={"form-control inputSearch"}
                        placeholder="E-mail"
                        name="email"
                        size={43}
                    />
                    <button className="product-filter-button-search">
                        <SearchIcon/>
                    </button>
                </div>
                <div className="product-filter-bottom-container">
                    <div className="product-filter-category-container">
                        <Controller name="papel"
                                    control={control}
                                    render={({field}) => (
                                        <Select {...field}
                                                options={selectPapeis}
                                                classNamePrefix="product-filter-select"
                                                onChange={value => handleChangeCategory(value as Papel)}
                                                getOptionLabel={(cat: Papel) => cat.autoridade}
                                                getOptionValue={(cat: Papel) => String(cat.id)}
                                                placeholder='Tipo'
                                                isClearable
                                        />
                                    )}
                        />
                    </div>
                    <button onClick={handleFormClear}
                            className="btn btn-outline-secondary btn-product-filter-clear"
                    >
                        LIMPAR<span className="btn-product-filter-word"> FILTROS</span>
                    </button>
                </div>
            </form>
        </div>
    );
}

export default UserFilter;