import {Link, useLocation} from 'react-router-dom';

import './styles.css';
import logoIfsc from "../../../assets/images/ifsc-logo.png";
import {ReactComponent as ArrowIcon} from "../../../assets/images/arrow.svg";

type Username = {
    username: string;
}

const ResetSuccess = () => {

    const username = useLocation<Username>().state?.username;

    if (username !== undefined) {
        return (
            <div className="base-card login-card">
                <div className="logo-mobile">
                    <img src={logoIfsc} alt="Logo do IFSC" className="img-card"/>
                    <h3>IFSC VAGAS</h3>
                    <h4>Câmpus Tubarão</h4>
                </div>

                <Link to="/auth">
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR PARA LOGIN</h2>
                    </div>
                </Link>

                <h1>RECUPERAR SENHA</h1>
                <h3>Senha alterada com sucesso!</h3>
            </div>
        );
    } else {
        return (
            <div className="base-card login-card">
                <div className="logo-mobile">
                    <img src={logoIfsc} alt="Logo do IFSC" className="img-card"/>
                    <h3>IFSC VAGAS</h3>
                    <h4>Câmpus Tubarão</h4>
                </div>

                <Link to="/auth">
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR PARA LOGIN</h2>
                    </div>
                </Link>

                <h1>RECUPERAR SENHA</h1>
                <h4 className="auth-error">Houve um problema ao identificar o usuário.</h4>
            </div>
        );
    }

};

export default ResetSuccess;
