import './styles.css';
import {Link, useHistory, useParams} from "react-router-dom";
import {ReactComponent as ArrowIcon} from "../../../assets/images/arrow.svg";
import {Controller, useForm} from "react-hook-form";
import {useEffect, useState} from "react";
import {requestBackend} from "../../../util/requests";
import Select from "react-select";
import {EmpresaFilterDTO, TipoVaga} from "../../../types/papel";
import CurrencyInput from "react-currency-input-field";
import {AxiosRequestConfig} from "axios";
import {toast} from "react-toastify";
import {Editor} from "react-draft-wysiwyg";
import {convertToRaw, EditorState} from "draft-js";
import draftToHtml from "draftjs-to-html";
import { stateFromHTML } from 'draft-js-import-html';

type FormData = {
    empresa: EmpresaFilterDTO | null;
    descricao: string | any;
    localDeTrabalho: string;
    titulo: string;
    remuneracao: number;
    tipos: TipoVaga[];
}

type UrlParams = {
    vagaId: string;
}

const toolbar = {
    options: ['inline', 'list'],
    inline: {
        bold: { className: 'custom-icon' },
        italic: {  className: 'custom-icon' },
        options: ['bold', 'italic'],
    },
    list: {
        options: ['unordered', 'ordered'],
        unordered: {  className: 'custom-icon' },
        ordered: {  className: 'custom-icon' }
    },
}

const NovaVagaAdmin = () => {

    const {vagaId} = useParams<UrlParams>();
    const isEditing = vagaId !== 'nova';
    const history = useHistory();
    const [selectTipos, setSelectTipos] = useState<TipoVaga[]>([]);
    const [selectEmpresa, setSelectEmpresa] = useState<EmpresaFilterDTO[]>([]);
    const {register, handleSubmit, formState: {errors}, setValue, control} = useForm<FormData>();
    const [editorState, setEditorState] = useState(EditorState.createEmpty());
    const onEditorStateChange = (editorState: EditorState) => {
        setEditorState(editorState);
    };

    useEffect(() => {
        requestBackend({url: '/tipos-vaga', withCredentials: true})
            .then(response => {
                setSelectTipos(response.data.content)
            })

        requestBackend({url: '/empresas', withCredentials: true})
            .then(response => {
                setSelectEmpresa(response.data)
            })
    }, []);

    useEffect(() => {
        if (isEditing) {
            requestBackend({url: `/vagas/${vagaId}`, withCredentials: true})
                .then((response) => {
                    const vaga = response.data as FormData;
                    const contentState = stateFromHTML(response.data.descricao);
                    const descricaoAsEditorState = EditorState.createWithContent(contentState);

                    setValue('empresa', vaga.empresa);
                    setValue('localDeTrabalho', vaga.localDeTrabalho);
                    setValue('remuneracao', vaga.remuneracao);
                    setValue('tipos', vaga.tipos);
                    setValue('titulo', vaga.titulo);
                    setValue('descricao', descricaoAsEditorState);

                    setEditorState(descricaoAsEditorState);
                })
        }
    }, [isEditing, vagaId, setValue]);

    const onSubmit = (formData: FormData) => {

        const data = {...formData, remuneracao: String(formData.remuneracao).replace(',', '.')};

        data.descricao = draftToHtml(convertToRaw(editorState.getCurrentContent()));

        if (data.remuneracao === 'undefined') {
            data.remuneracao = '0';
        }

        const config: AxiosRequestConfig = {
            // proximos passos: fazer endpoint de atualizacao, testar atualizacao, fazer pagina de detalhes da vaga, implementar editor de texto bonitinho
            method: isEditing ? 'PUT' : 'POST',
            url: isEditing ? `/vagas/${vagaId}` : '/vagas',
            data,
            withCredentials: true
        };

        requestBackend(config)
            .then(response => {
                {!isEditing && toast.success('Vaga cadastrada com sucesso');}
                {isEditing && toast.success('Vaga alterada com sucesso');}
                history.push("/app/vagas-admin");
            })
            .catch(() => {
                toast.error("Erro ao cadastrar a vaga");
            });
    };

    return (


        <div className="product-crud-container">
            <div className="base-card product-crud-form-card">
                <Link to="/app/vagas-admin">
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR</h2>
                    </div>
                </Link>
                {!isEditing && <h1 className="product-crud-form-title">CADASTRO DE VAGA</h1>}
                {isEditing && <h1 className="product-crud-form-title">EDIÇÃO DE VAGA</h1>}
                <form onSubmit={handleSubmit(onSubmit)} autoComplete="off">

                    <div className="row product-crud-inputs-container">
                        <div className="col-lg-6 product-crud-inputs-left-container">
                            <div className="mb-4">
                                <Controller name="empresa"
                                            rules={{required: true}}
                                            control={control}
                                            render={({field}) => (
                                                <Select {...field}
                                                        options={selectEmpresa}
                                                        classNamePrefix="product-crud-select"
                                                        getOptionLabel={(empresa: EmpresaFilterDTO) => empresa.nome}
                                                        getOptionValue={(empresa: EmpresaFilterDTO) => String(empresa.id)}
                                                        placeholder='Empresa*'
                                                        isDisabled={isEditing}
                                                />
                                            )}
                                />
                                {
                                    errors.empresa &&
                                    <div className="invalid-feedback d-block">Campo obrigatório</div>
                                }
                            </div>
                            {/*--------------------------------------------------------------------------------------------------------------------------------------------------------------*/}
                            <div className="mb-4">
                                <input
                                    {...register("titulo", {
                                        required: 'Campo obrigatório'
                                    })}
                                    type="text"
                                    className={`form-control base-input ${errors.titulo ? 'is-invalid' : ''}`}
                                    placeholder="Título da Vaga*"
                                    name="titulo"
                                />
                                <div className="invalid-feedback d-block">{errors.titulo?.message}</div>
                            </div>
                            {/*--------------------------------------------------------------------------------------------------------------------------------------------------------------*/}
                            <div className="mb-4">
                                <input
                                    {...register("localDeTrabalho")}
                                    type="text"
                                    className={`form-control base-input ${errors.localDeTrabalho ? 'is-invalid' : ''}`}
                                    placeholder="Local de trabalho"
                                    name="localDeTrabalho"
                                />
                            </div>
                            {/*--------------------------------------------------------------------------------------------------------------------------------------------------------------*/}
                            <div className="mb-4">
                                <Controller
                                    name="remuneracao"
                                    control={control}
                                    render={({field}) => (
                                        <CurrencyInput
                                            placeholder='Remuneração (R$)'
                                            className={`form-control base-input ${errors.remuneracao ? 'is-invalid' : ''}`}
                                            disableGroupSeparators={true}
                                            value={field.value}
                                            onValueChange={field.onChange}
                                        />
                                    )}
                                />
                            </div>
                            {/*--------------------------------------------------------------------------------------------------------------------------------------------------------------*/}
                            <div className="mb-4">
                                <Controller name="tipos"
                                            rules={{required: true}}
                                            control={control}
                                            render={({field}) => (
                                                <Select {...field}
                                                        options={selectTipos}
                                                        classNamePrefix="product-crud-select"
                                                        isMulti
                                                        getOptionLabel={(tipo: TipoVaga) => tipo.descricao}
                                                        getOptionValue={(tipo: TipoVaga) => String(tipo.id)}
                                                        placeholder='Tipos da vaga...*'
                                                />
                                            )}
                                />
                                {
                                    errors.tipos &&
                                    <div className="invalid-feedback d-block">Campo obrigatório</div>
                                }
                            </div>
                        </div>

                        {/*<div className="col-lg-6">*/}
                        {/*    <div>*/}
                        {/*        <textarea*/}
                        {/*            rows={14}*/}
                        {/*            {...register("descricao")}*/}
                        {/*            className={`form-control base-input h-auto ${errors.descricao ? 'is-invalid' : ''}`}*/}
                        {/*            placeholder="Descrição"*/}
                        {/*            name="descricao"*/}
                        {/*        />*/}
                        {/*    </div>*/}
                        {/*</div>*/}

                        <div className="col-lg-6">
                            <div>

                                <Controller
                                    control={control}
                                    name="descricao"
                                    render={({field}) => (

                                        <Editor
                                            editorState={editorState}
                                            toolbarClassName="toolbar-container"
                                            editorClassName="editor-container  base-input h-auto vaga"
                                            onEditorStateChange={onEditorStateChange}
                                            toolbar={toolbar}
                                            placeholder='Descrição'
                                            wrapperClassName='vaga'
                                        />
                                    )}
                                />

                            </div>
                        </div>

                    </div>
                    <div className="product-crud-buttons-container">
                        <button className="btn btn-primary product-crud-button text-white botoes">
                            SALVAR
                        </button>
                    </div>
                </form>
            </div>
        </div>

    );
};

export default NovaVagaAdmin;