import './styles.css';
import StudentImg from '../../../assets/images/student-icon.png';
import CompanyImg from '../../../assets/images/company-icon.png';
import AdminImg from '../../../assets/images/admin-icon.png';
import CategoryBadge from "../../Solicitacoes/CategoryBadge";
import {UsuarioDTO} from "../../../types/papel";
import {useEffect, useState} from "react";
import {Link} from 'react-router-dom';

type Props = {
    usuario: UsuarioDTO;
}

const CardUsuario = ({usuario}: Props) => {

    const [papel, setpapel] = useState<string>();

    useEffect(() => {
        if (usuario.papeis[0].autoridade === 'ROLE_ADMIN') {
            setpapel('Administrador')
        } else if (usuario.papeis[0].autoridade === 'ROLE_EMPRESA') {
            setpapel('Empresa')
        } else if (usuario.papeis[0].autoridade === 'ROLE_ESTUDANTE') {
            setpapel('Estudante')
        }
    }, [usuario])

    return (
        <div className="base-card product-crud-card">
            <div className="product-crud-card-top-container">
                {papel === 'Estudante' && <img src={StudentImg} alt="Estudante"/>}
                {papel === 'Empresa' && <img src={CompanyImg} alt="Empresa"/>}
                {papel === 'Administrador' && <img src={AdminImg} alt="Administrador"/>}
            </div>
            <div className="product-crud-card-description">
                <div className="product-crud-card-bottom-container">
                    <div className="frase">
                        <h6>
                            <text className="title">Nome: </text>
                            {usuario.nome}</h6>
                    </div>
                    <div className="frase">
                        <h6>
                            <text className="title">E-mail: </text>
                            {usuario.email}</h6>
                    </div>
                    {usuario.nomeEmpresa && <div className="frase">
                        <h6>
                            <text className="title">Empresa: </text>
                            {usuario.nomeEmpresa}</h6>
                    </div>}
                    {usuario.ativo && <div className="frase">
                        <h6>
                            <text className="title">Status: </text>
                            Ativo</h6>
                    </div>}
                    {!usuario.ativo && <div className="frase">
                        <h6>
                            <text className="title">Status: </text>
                            <text className="text-danger">Inativo</text></h6>
                    </div>}
                </div>
                <div className="product-crud-categories-container">
                    <CategoryBadge name={papel}/>
                </div>
            </div>
            <div className="product-crud-card-buttons-container">
                {/*onClick={handleApproveClick}*/}
                <Link to={`/app/usuario/${usuario.id}`}>
                    <button className="btn-outline-primary product-crud-card-button">
                        DETALHES
                    </button>
                </Link>
            </div>
        </div>
    );
}

export default CardUsuario;