import {ReactComponent as ArrowIcon} from 'assets/images/arrow.svg';
import {Link, useHistory, useParams} from 'react-router-dom';
import './styles.css';
import {useContext, useEffect, useState} from "react";
import {CandidaturaDTO} from "../../../types/papel";
import {requestBackend, requestBackendVisualizacoesCandidaturasPendentes} from "../../../util/requests";
import {EditorState} from "draft-js";
import {VisualizacaoCandidaturasPendentesContext} from "../../../VisualizacaoCandidaturasPendentesContext";
import {stateFromHTML} from "draft-js-import-html";
import {Editor} from "react-draft-wysiwyg";
import CategoryBadge from "../../Solicitacoes/CategoryBadge";
import {AxiosRequestConfig} from "axios";
import FileSaver from "file-saver";


type UrlParams = {
    candidaturaId: string;
}

export const formatPrice = (price: number) => {
    if (price !== 0.0) {
        const params = {minimumFractionDigits: 2, maximumFractionDigits: 2};

        return new Intl.NumberFormat('pt-BR', params).format(price);
    }
}

const DetalhesCandidaturaAdmin = () => {
    const history = useHistory();

    const handleDownloadCurriculo = (candidaturaId: number | undefined) => {
        const config: AxiosRequestConfig = {
            method: 'GET',
            url: `/candidaturas/${candidaturaId}/curriculo`,
            withCredentials: true,
            responseType:"arraybuffer"
        };

        requestBackend(config)
            .then(response => {
                var blob = new Blob([response.data], {type: "application/pdf;charset=utf-8"});
                FileSaver.saveAs(blob, (candidatura?.estudante.nome + ' - Currículo (IFSC Vagas)').replaceAll(' ', '_'));
            })
    }

    const {candidaturaId} = useParams<UrlParams>();

    const {
        visualizacaoCandidaturasPendentes,
        setVisualizacaoCandidaturasPendentes
    } = useContext(VisualizacaoCandidaturasPendentesContext);

    const [candidatura, setCandidatura] = useState<CandidaturaDTO>();

    const [editorState, setEditorState] = useState(EditorState.createEmpty());
    const onEditorStateChange = (editorState: EditorState) => {
        setEditorState(editorState);
    };

    // const [papel, setpapel] = useState<string>();

    useEffect(() => {
        requestBackend({url: `/candidaturas/${candidaturaId}`, withCredentials: true})
            .then(response => {
                setCandidatura(response.data)
                const contentState = stateFromHTML(response.data.vagaDTO.descricao || '');
                const descricaoAsEditorState = EditorState.createWithContent(contentState);
                setEditorState(descricaoAsEditorState);
            })
    }, [candidaturaId]);

    const handleRedirectCandidaturas = (vagaId: number | undefined) => {
        history.push('/app/candidaturas-admin', {vagaId: vagaId})
    }

    return (
        <div className="product-details-container candidatura">

            <div className="base-card product-details-card">
                {/*<Link to="/app/candidaturas-admin">*/}
                <button
                    onClick={() => handleRedirectCandidaturas(candidatura?.vagaDTO.id)}
                className="botaoVoltar">
                <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR</h2>
                    </div>
                </button>
                {/*</Link>*/}
                <div className="row">

                    <div className="col-xl-6">
                        <div className="detalhesVaga">
                            <p>
                                Estudante: {candidatura?.estudante.nome}
                            </p>
                            <p>
                                Data da candidatura: {candidatura?.dataCandidatura}
                            </p>
                            <p>
                                <button
                                    onClick={() => handleDownloadCurriculo(candidatura?.id)}
                                    className="btn-outline-primary product-crud-card-button product-crud-card-button-first">
                                    BAIXAR CURRÍCULO
                                </button>
                            </p>
                        </div>
                    </div>


                    <div className="col-xl-6">
                        <div className="detalhesVaga">
                            <p>
                                Empresa: {candidatura?.vagaDTO.empresa.nome}
                            </p>
                            <p>
                                Vaga: {candidatura?.vagaDTO.titulo}
                            </p>
                            {candidatura?.vagaDTO.localDeTrabalho &&
                                <p>
                                    Local de trabalho: {candidatura?.vagaDTO.localDeTrabalho}
                                </p>
                            }
                            { candidatura?.vagaDTO.remuneracao && candidatura?.vagaDTO.remuneracao > 0 ?
                                <p>
                                    Remuneração: R$ {formatPrice(candidatura?.vagaDTO.remuneracao)}
                                </p>
                                :
                                null
                            }
                            <div className="product-crud-categories-container">
                                {
                                    candidatura?.vagaDTO.tipos.map(tipo => (
                                        <CategoryBadge name={tipo.descricao} key={tipo.id}/>
                                    ))
                                }
                            </div>
                        </div>

                        { candidatura?.vagaDTO.descricao && candidatura?.vagaDTO.descricao.length > 8 &&
                            <Editor
                                editorState={editorState}
                                editorClassName="editor-container  description-container description-container-vaga base-input h-auto"
                                toolbarHidden
                                readOnly
                            />
                        }

                        </div>

                    {/*<div className="col-xl-6">*/}
                {/*        <div className="description-container">*/}
                {/*            <h2>{vaga?.titulo}</h2>*/}
                {/*            <p>Empresa: {vaga?.empresa.nome}</p>*/}
                {/*            {vaga?.localDeTrabalho && <p>Local de trabalho: {vaga.localDeTrabalho}</p>}*/}
                {/*            {vaga?.remuneracao && <p>Remuneração: R$ {formatPrice(vaga.remuneracao)}</p>}*/}
                {/*            <div className="product-crud-categories-container">*/}
                {/*                {*/}
                {/*                    vaga?.tipos.map(tipo => (*/}
                {/*                        <CategoryBadge name={tipo.descricao} key={tipo.id}/>*/}
                {/*                    ))*/}
                {/*                }*/}
                {/*            </div>*/}
                {/*        </div>*/}
                {/*        <div className="botao-candidatar">*/}
                {/*            {*/}
                {/*                !vaga?.estudanteJaCandidatado &&*/}
                {/*                <Link to={`/app/candidatar/${vagaId}`}>*/}
                {/*                    <ButtonIcon text="Candidate-se"/>*/}
                {/*                </Link>*/}
                {/*            }*/}
                {/*            {*/}
                {/*                vaga?.estudanteJaCandidatado &&*/}
                {/*                <div className="diminuir">*/}
                {/*                    <p>*/}
                {/*                        Sua candidatura já foi realizada para essa vaga.*/}
                {/*                    </p>*/}
                {/*                </div>*/}

                {/*            }*/}
                {/*        </div>*/}
                {/*    </div>*/}
                </div>
            </div>
        </div>
    );
}

export default DetalhesCandidaturaAdmin;