import {NavLink} from 'react-router-dom';
import './styles.css';
import {useContext, useEffect, useState} from "react";
import {hasAnyRoles, isAuthenticated} from "../../util/auth";
import {SolicitacoesPendentesContext} from "../../SolicitacoesPendentesContext";
import {VisualizacaoCandidaturasPendentesContext} from "../../VisualizacaoCandidaturasPendentesContext";

const SideNavbar = () => {

    const {solicitacoesPendentes, setSolicitacoesPendentes} = useContext(SolicitacoesPendentesContext);
    const {visualizacaoCandidaturasPendentes, setVisualizacaoCandidaturasPendentes} = useContext(VisualizacaoCandidaturasPendentesContext);

    return (
        <nav className="admin-nav-container">
            <ul className="admin-nav-itens-container">

                { /*MENU ADMINISTRADOR*/ }
                { hasAnyRoles(["ROLE_ADMIN"]) &&
                    <>
                        <li>
                            <NavLink to="/app/usuarios" className="admin-nav-item">
                                <p>Usuários</p>
                            </NavLink>
                        </li>
                        <li>
                            <NavLink to="/app/solicitacoes" className="admin-nav-item">
                                <p>Solicitações</p>
                                {   solicitacoesPendentes !== undefined && solicitacoesPendentes > 0 && isAuthenticated() && hasAnyRoles(["ROLE_ADMIN"]) &&
                                <div className="badge">{solicitacoesPendentes}</div>
                                }
                            </NavLink>
                        </li>
                        <li>
                            <NavLink to="/app/vagas-admin" className="admin-nav-item">
                                <p>Vagas</p>
                            </NavLink>
                        </li>
                    </>
                }

                { /*MENU EMPRESA*/ }
                { hasAnyRoles(["ROLE_EMPRESA"]) &&
                    <>
                        <li>
                            <NavLink to="/app/vagas-empresa" className="admin-nav-item">
                                <p>Minhas vagas</p>
                            </NavLink>
                        </li>
                        <li>
                            <NavLink to="/app/candidaturas" className="admin-nav-item">
                                <p>Candidaturas</p>
                                {   visualizacaoCandidaturasPendentes !== undefined && visualizacaoCandidaturasPendentes > 0 && isAuthenticated() && hasAnyRoles(["ROLE_EMPRESA"]) &&
                                    <div className="badge">{visualizacaoCandidaturasPendentes}</div>
                                }
                            </NavLink>
                        </li>
                    </>
                }

                { /*MENU ESTUDANTE*/ }
                { hasAnyRoles(["ROLE_ESTUDANTE"]) &&
                    <>
                        <li>
                            <NavLink to="/app/vagas" className="admin-nav-item">
                                <p>Visualizar vagas</p>
                            </NavLink>
                        </li>
                    </>
                }

                { /*MENU TODOS USUARIOS*/ }
                <li>
                    <NavLink to="/app/alterar-senha" className="admin-nav-item">
                        <p>Alterar senha</p>
                    </NavLink>
                </li>
                <li>
                    <NavLink to="/app/enviar-feedback" className="admin-nav-item">
                        <p>Enviar feedback</p>
                    </NavLink>
                </li>
            </ul>
        </nav>
    );
}

export default SideNavbar;