import {RequisicaoCadastroDTO} from "../../../types/RequisicaoCadastroDTO";
import CategoryBadge from "../CategoryBadge";
import './styles.css';
import StudentImg from '../../../assets/images/student-icon.png';
import CompanyImg from '../../../assets/images/company-icon.png';
import {requestApproveRegister, requestDenyRegister} from "../../../util/requests";
import {toast} from 'react-toastify';

type Props = {
    solicitacao: RequisicaoCadastroDTO;
    onFeedback: Function;
}

const CardSolicitacaoAprovar = ({solicitacao, onFeedback}: Props) => {

    const handleApproveClick = (event: React.MouseEvent<HTMLAnchorElement>) => {
        event.preventDefault();
        requestApproveRegister(solicitacao.id)
            .then(response => {
                toast.success(`Cadastro do usuário ${solicitacao.email} aprovado!`);
                onFeedback();
                // window.location.reload();
            })
            .catch(() => {
                toast.error("Erro ao aprovar o cadastro");
            });
    }

    const handleDenyClick = (event: React.MouseEvent<HTMLAnchorElement>) => {
        event.preventDefault();
        requestDenyRegister(solicitacao.id)
            .then(response => {
                toast.warn(`Cadastro do usuário ${solicitacao.email} não aprovado.`);
                onFeedback();
                // window.location.reload();
            })
            .catch(() => {
                toast.error("Erro ao aprovar o cadastro");
            });
    }


    return (
        <div className="base-card product-crud-card">
            <div className="product-crud-card-top-container">
                {solicitacao.tipo === "ESTUDANTE" && <img src={StudentImg} alt="Estudante"/>}
                {solicitacao.tipo === "EMPRESA" && <img src={CompanyImg} alt="Empresa"/>}
            </div>
            <div className="product-crud-card-description">
                <div className="product-crud-card-bottom-container">
                    <div className="frase">
                        <h6>
                            <text className="title">Nome:</text>
                            {solicitacao.nomeUsuario}</h6>
                    </div>
                    <div className="frase">
                        <h6>
                            <text className="title">E-mail:</text>
                            {solicitacao.email}</h6>
                    </div>
                    {solicitacao.tipo === "EMPRESA" && <div className="frase">
                        <h6>
                            <text className="title">Empresa:</text>
                            {solicitacao.nomeEmpresa}</h6>
                    </div>}
                    {solicitacao.tipo === "EMPRESA" && <div className="frase">
                        <h6>
                            <text className="title">CNPJ:</text>
                            {solicitacao.cnpj}</h6>
                    </div>}
                    {solicitacao.tipo === "EMPRESA" && <div className="frase">
                        <h6>
                            <text className="title">Telefone:</text>
                            {solicitacao.telefone}</h6>
                    </div>}
                </div>
                <div className="product-crud-categories-container">
                    <CategoryBadge name={solicitacao.tipo}/>
                </div>
            </div>
            <div className="product-crud-card-buttons-container">
                <a href="#" onClick={handleDenyClick}>
                    <button className="btn-outline-danger product-crud-card-button product-crud-card-button-first">
                        NEGAR
                    </button>
                </a>
                <a href="#" onClick={handleApproveClick}>
                    <button className="btn-outline-primary product-crud-card-button">
                        APROVAR
                    </button>
                </a>
            </div>
        </div>
    );
}

export default CardSolicitacaoAprovar;