import './styles.css';
import {ReactComponent as SearchIcon} from "assets/images/search-icon.svg";
import {Controller, useForm} from "react-hook-form";
import Select from "react-select";
import {useEffect, useState} from "react";
import {EmpresaFilterDTO, Papel, TipoVaga} from "../../../types/papel";
import {requestBackend} from "../../../util/requests";

export type VagasAdminFilterData = {
    titulo: string;
    empresa: EmpresaFilterDTO | null;
    tipoVaga: TipoVaga | null;
}

type Props = {
    onSubmitFilter: (data: VagasAdminFilterData) => void;
}

const VagasAdminFilter = ({onSubmitFilter}: Props) => {

    const [selectTiposVaga, setSelectTiposVaga] = useState<TipoVaga[]>([]);
    const [selectEmpresa, setSelectEmpresa] = useState<EmpresaFilterDTO[]>([]);

    const {
        register,
        handleSubmit,
        control,
        setValue,
        getValues
    } = useForm<VagasAdminFilterData>();

    const onSubmit = (formData: VagasAdminFilterData) => {
        onSubmitFilter(formData);
    }

    const handleFormClear = () => {
        setValue('titulo', '');
        setValue('empresa', null);
        setValue('tipoVaga', null);
    }

    const handleChangeTipoVaga = (value: TipoVaga) => {
        setValue('tipoVaga', value);

        const obj: VagasAdminFilterData = {
            titulo: getValues('titulo'),
            empresa: getValues('empresa'),
            tipoVaga: getValues('tipoVaga')
        };

        onSubmitFilter(obj);
    }

    const handleChangeEmpresa = (value: EmpresaFilterDTO) => {
        setValue('empresa', value);

        const obj: VagasAdminFilterData = {
            titulo: getValues('titulo'),
            empresa: getValues('empresa'),
            tipoVaga: getValues('tipoVaga')
        };

        onSubmitFilter(obj);
    }

    useEffect(() => {
        requestBackend({url: '/tipos-vaga', withCredentials: true})
            .then(response => {
                setSelectTiposVaga(response.data.content)
            })

        requestBackend({url: '/empresas', withCredentials: true})
            .then(response => {
                setSelectEmpresa(response.data)
            })
    }, [])

    return (
        <div className="base-card product-filter-container">
            <form onSubmit={handleSubmit(onSubmit)} className="product-filter-form">
                <div className="product-filter-name-container">
                    <input
                        {...register("titulo")}
                        type="text"
                        className={"form-control inputSearch"}
                        placeholder="Título"
                        name="titulo"
                        size={43}
                    />
                    <button className="product-filter-button-search">
                        <SearchIcon/>
                    </button>
                </div>

                <div className="product-filter-bottom-container">
                    <div className="product-filter-category-container">
                        <Controller name="empresa"
                                    control={control}
                                    render={({field}) => (
                                        <Select {...field}
                                                options={selectEmpresa}
                                                classNamePrefix="product-filter-select"
                                                onChange={value => handleChangeEmpresa(value as EmpresaFilterDTO)}
                                                getOptionLabel={(empresa: EmpresaFilterDTO) => empresa.nome}
                                                getOptionValue={(empresa: EmpresaFilterDTO) => String(empresa.id)}
                                                placeholder='Empresa'
                                                isClearable
                                        />
                                    )}
                        />
                    </div>
                </div>

                <div className="product-filter-bottom-container">
                    <div className="product-filter-category-container">
                        <Controller name="tipoVaga"
                                    control={control}
                                    render={({field}) => (
                                        <Select {...field}
                                                options={selectTiposVaga}
                                                classNamePrefix="product-filter-select"
                                                onChange={value => handleChangeTipoVaga(value as TipoVaga)}
                                                getOptionLabel={(cat: TipoVaga) => cat.descricao}
                                                getOptionValue={(cat: TipoVaga) => String(cat.id)}
                                                placeholder='Tipo'
                                                isClearable
                                        />
                                    )}
                        />
                    </div>
                    <button onClick={handleFormClear}
                            className="btn btn-outline-secondary btn-product-filter-clear"
                    >
                        LIMPAR<span className="btn-product-filter-word"> FILTROS</span>
                    </button>
                </div>
            </form>
        </div>
    );
}

export default VagasAdminFilter;