import './styles.css';
import {Link, useHistory} from "react-router-dom";
import {ReactComponent as ArrowIcon} from "../../../assets/images/arrow.svg";
import ButtonIcon from "../../../components/ButtonIcon";
import {useForm} from "react-hook-form";
import {useState} from "react";
import {requestNovoEmpresaManual} from "../../../util/requests";
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

type FormData = {
    nomeEmpresa: string;
    email: string;
    cnpj: string;
    nomeUsuario: string;
    senha: string;
}

const cnpjMask = (value: any) => {
    return value
        .replace(/\D+/g, '') // não deixa ser digitado nenhuma letra
        .replace(/(\d{2})(\d)/, '$1.$2') // captura 2 grupos de número o primeiro com 2 digitos e o segundo de com 3 digitos, apos capturar o primeiro grupo ele adiciona um ponto antes do segundo grupo de número
        .replace(/(\d{3})(\d)/, '$1.$2')
        .replace(/(\d{3})(\d)/, '$1/$2') // captura 2 grupos de número o primeiro e o segundo com 3 digitos, separados por /
        .replace(/(\d{4})(\d)/, '$1-$2')
        .replace(/(-\d{2})\d+?$/, '$1') // captura os dois últimos 2 números, com um - antes dos dois números
}

const NovoEmpresa = () => {

    const [values, setValues] = useState({cnpj: ''})

    const inputChange = (e: any) => {
        const {name, value} = e.target
        setValues({
            ...values,
            [name]: value
        })
    }

    const history = useHistory();

    const {register, handleSubmit, formState: {errors}} = useForm<FormData>();

    const [hasRequestError, setHasRequestError] = useState(false);

    const [showPassword, setShowPassword] = useState(false);

    const handlePasswordClick = (event: React.MouseEvent<HTMLAnchorElement>) => {
        event.preventDefault();
        setShowPassword(!showPassword);
    }

    const onSubmit = (formData: FormData) => {
        requestNovoEmpresaManual(formData)
            .then(response => {
                history.push('/app/cadastro-sucesso', {email: formData.email})
            })
            .catch(error => {
                setHasRequestError(true);
            });
    };

    const handleKeyPress = () => {
        setHasRequestError(false)
    }

    const handleKeySenha = (event: any) => {
        if (event.key === 'Enter') {
            event.preventDefault();
            const teste = document.getElementById('enviar');
            if (teste != null) {
                teste.click();
            }
        }
    }

    return (
        <div className="base-card login-card geral">
            <Link to="/app/novo-usuario">
                <div className="goback-container">
                    <ArrowIcon/>
                    <h2>VOLTAR</h2>
                </div>
            </Link>

            <h1>CADASTRO MANUAL DE EMPRESA</h1>

            <form onSubmit={handleSubmit(onSubmit)} autoComplete="off">
                <div className="mb-4">
                    <input
                        {...register("nomeEmpresa", {
                            required: 'Campo obrigatório'
                        })}
                        type="text"
                        className={`form-control base-input ${errors.nomeEmpresa ? 'is-invalid' : ''}`}
                        placeholder="Nome da empresa"
                        name="nomeEmpresa"
                    />
                    <div className="invalid-feedback d-block">{errors.nomeEmpresa?.message}</div>
                </div>
                <div className="mb-4">
                    <input
                        {...register("email", {
                            required: 'Campo obrigatório',
                            pattern: {
                                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                message: 'E-mail inválido'
                            }
                        })}
                        type="text"
                        className={`form-control base-input ${errors.email ? 'is-invalid' : ''}`}
                        placeholder="E-mail"
                        name="email"
                        onChange={handleKeyPress}
                    />
                    <div className="invalid-feedback d-block">{errors.email?.message}</div>
                </div>

                <div className="mb-4">
                    <input
                        {...register("cnpj", {
                            required: 'Campo obrigatório',
                            pattern: {
                                value: /^\d{2}\.\d{3}\.\d{3}\/\d{4}\-\d{2}$/,
                                // https://www.youtube.com/watch?v=hWukAkEzBB4&ab_channel=ViniciusDacal TODO: implementar
                                message: 'CNPJ inválido'
                            }
                        })}
                        type="text"
                        className={`form-control base-input ${errors.cnpj ? 'is-invalid' : ''}`}
                        placeholder="CNPJ"
                        name="cnpj"
                        value={cnpjMask(values.cnpj)}
                        onChange={inputChange}
                    />
                    <div className="invalid-feedback d-block">{errors.cnpj?.message}</div>
                </div>

                <div className="mb-4">
                    <input
                        {...register("nomeUsuario", {
                            required: 'Campo obrigatório'
                        })}
                        type="text"
                        className={`form-control base-input ${errors.nomeUsuario ? 'is-invalid' : ''}`}
                        placeholder="Nome de quem utilizará o sistema"
                        name="nomeUsuario"
                    />
                    <div className="invalid-feedback d-block">{errors.nomeUsuario?.message}</div>
                </div>

                <div className="mb-2 input-group">
                    <input
                        {...register("senha", {
                            required: 'Campo obrigatório'
                        })}
                        type={showPassword ? "text" : "password"}
                        className={`form-control base-input ${errors.senha ? 'is-invalid' : ''}`}
                        placeholder="Senha de acesso"
                        name="senha"
                        autoComplete="off"
                        onKeyPress={handleKeySenha}
                    />
                    <a href="#" onClick={handlePasswordClick}>
                        <button className="input-group-text bg-dark text-light form-control base-input visao-senha">
                            {showPassword ? <VisibilityIcon/> : <VisibilityOffIcon/>}
                        </button>
                    </a>
                    <div className="invalid-feedback d-block">{errors.senha?.message}</div>
                </div>

                {hasRequestError &&
                    <div className="auth-error">
                        <p>Erro ao realizar cadastro. Por favor, confira se o e-mail ou o CNPJ já está sendo
                            utilizado.</p>
                    </div>
                }

                <div className="login-submit">
                    <ButtonIcon text="Enviar"/>
                </div>
            </form>
        </div>
    );
};

export default NovoEmpresa;