import {ReactComponent as ArrowIcon} from 'assets/images/arrow.svg';
import {Link, useHistory, useParams} from 'react-router-dom';
import './styles.css';
import {requestBackend} from "../../../util/requests";
import ButtonIcon from "../../../components/ButtonIcon";
import {useForm} from "react-hook-form";
import {AxiosRequestConfig} from "axios";
import {toast} from "react-toastify";


type UrlParams = {
    vagaId: string;
}

type FormDataa = {
    arquivo: any;
}

const CandidatarVagaEstudante = () => {

    var bodyFormData = new FormData();

    const {vagaId} = useParams<UrlParams>();

    const history = useHistory();

    const {handleSubmit} = useForm<FormData>();

    const onFileChange = (e: any) => {
        if (e.target && e.target.files[0]) {
            // arquivo = e.target.files[0]
            bodyFormData.append('arquivo', e.target.files[0])
        }
    }

    const onSubmit = (formData: FormDataa) => {
        if (bodyFormData.has('arquivo')) {
            const config: AxiosRequestConfig = {
                method: 'POST',
                url: `/candidaturas/${vagaId}`,
                withCredentials: true,
                data: bodyFormData,
                headers: {"Content-Type": "multipart/form-data"}
            };

            requestBackend(config)
                .then(response => {
                        history.push("/app/sucesso-candidatura");
                    }
                )
                .catch(error => {
                    toast.error(error.response.data.mensagem)
                });
        } else {
            toast.error("Nenhum arquivo selecionado")
        }


    }

    return (
        <div className="product-details-container">

            <div className="base-card product-details-card all">
                <Link to={`/app/vaga/${vagaId}`}>
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR</h2>
                    </div>
                </Link>
                <div className="row">

                    <div className="col-xl-12">

                        <form onSubmit={handleSubmit(onSubmit)} autoComplete="off">

                            <h4>
                                Para se candidatar na vaga selecionada, envie o seu currículo no formato PDF.
                            </h4>
                            <br/>
                            <h4>
                                Dica: mantenha em seu currículo suas formas de contato atualizadas, para que a empresa
                                possa entrar em contato.
                            </h4>
                            <br/>

                            <div>
                                <input
                                    className="form-control form-control-lg"
                                    id="formFileLg"
                                    type="file"
                                    onChange={onFileChange}
                                    accept=".pdf"
                                />
                            </div>

                            <div className="login-submit">
                                <ButtonIcon text="Enviar"/>
                            </div>
                        </form>

                    </div>
                </div>
            </div>
        </div>
    );
}

export default CandidatarVagaEstudante;