import './styles.css';
import {Link, useHistory} from "react-router-dom";
import {ReactComponent as ArrowIcon} from "../../assets/images/arrow.svg";
import {Controller, useForm} from "react-hook-form";
import {requestEnvioFeedback} from "../../util/requests";
import ButtonIcon from "../../components/ButtonIcon";
import {toast} from "react-toastify";
import {Editor} from "react-draft-wysiwyg";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import draftToHtml from "draftjs-to-html";
import {convertToRaw, EditorState} from "draft-js";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import {useState} from "react";

type FormData = {
    feedback: string;
}

const toolbar = {
    options: ['inline', 'list'],
    inline: {
        bold: { className: 'custom-icon' },
        italic: {  className: 'custom-icon' },
        options: ['bold', 'italic'],
    },
    list: {
        options: ['unordered', 'ordered'],
        unordered: {  className: 'custom-icon' },
        ordered: {  className: 'custom-icon' }
    },
}

const EnviarFeedback = () => {

    const [editorState, setEditorState] = useState(EditorState.createEmpty());

    const onEditorStateChange = (editorState: EditorState) => {
        setEditorState(editorState);
    };

    const history = useHistory();

    const {register, handleSubmit, formState: {errors}, control} = useForm<FormData>();

    const onSubmit = (formData: FormData) => {
        formData.feedback = draftToHtml(convertToRaw(editorState.getCurrentContent()));

        requestEnvioFeedback(formData)
            .then(response => {
                history.push('/app/home')
                toast.success(`Feedback enviado com sucesso`);
            })
            .catch(error => {
                toast.error(`Erro ao enviar feedback`);
            });
    };

    return (
        <div className="base-card login-card geral">
            <Link to="/app/home">
                <div className="goback-container">
                    <ArrowIcon/>
                    <h2>CANCELAR</h2>
                </div>
            </Link>

            <h1>ENVIAR FEEDBACK</h1>

            <p>Agradecemos desde já o seu feedback para estarmos melhorando o nosso sistema nas próximas atualizações.</p>

            <form onSubmit={handleSubmit(onSubmit)} autoComplete="off">
                {/*<div className="col-lg-12">*/}
                {/*    <div>*/}
                {/*                <textarea*/}
                {/*                    rows={14}*/}
                {/*                    {...register("feedback")}*/}
                {/*                    className={`form-control base-input h-auto ${errors.feedback ? 'is-invalid' : ''}`}*/}
                {/*                    placeholder="Descreva sua experiência aqui"*/}
                {/*                    name="feedback"*/}
                {/*                />*/}
                {/*    </div>*/}
                {/*</div>*/}

                <div className="col-lg-12">
                    <div>

                        <Controller
                            control={control}
                            name="feedback"
                            render={({field}) => (

                                <Editor
                                    editorState={editorState}
                                    toolbarClassName="toolbar-container"
                                    editorClassName="editor-container  base-input h-auto"
                                    onEditorStateChange={onEditorStateChange}
                                    toolbar={toolbar}
                                    placeholder='Descreva sua experiência aqui'
                                />
                            )}
                        />

                    </div>
                </div>

                <div className="login-submit">
                    <ButtonIcon text="Enviar"/>
                </div>
            </form>
        </div>
    );
};

export default EnviarFeedback;