import './styles.css';
import {Link, useHistory} from "react-router-dom";
import {ReactComponent as ArrowIcon} from "../../../assets/images/arrow.svg";
import ButtonIcon from "../../../components/ButtonIcon";
import {useForm} from "react-hook-form";
import {useState} from "react";
import {requestNovoEstudanteManual} from "../../../util/requests";
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

type FormData = {
    nome: string;
    email: string;
    senha: string;
}

const NovoEstudante = () => {

    const history = useHistory();

    const {register, handleSubmit, formState: {errors}} = useForm<FormData>();

    const [hasRequestError, setHasRequestError] = useState(false);

    const [showPassword, setShowPassword] = useState(false);

    const handlePasswordClick = (event: React.MouseEvent<HTMLAnchorElement>) => {
        event.preventDefault();
        setShowPassword(!showPassword);
    }

    const onSubmit = (formData: FormData) => {
        requestNovoEstudanteManual(formData)
            .then(response => {
                history.push('/app/cadastro-sucesso', {email: formData.email})
            })
            .catch(error => {
                setHasRequestError(true);
            });
    };

    const handleKeyPress = () => {
        setHasRequestError(false)
    }

    const handleKeySenha = (event: any) => {
        if (event.key === 'Enter') {
            event.preventDefault();
            const teste = document.getElementById('enviar');
            if (teste != null) {
                teste.click();
            }
        }
    }

    return (
        <div className="base-card login-card geral">
            <Link to="/app/novo-usuario">
                <div className="goback-container">
                    <ArrowIcon/>
                    <h2>VOLTAR</h2>
                </div>
            </Link>

            <h1>CADASTRO MANUAL DE ESTUDANTE</h1>

            <form onSubmit={handleSubmit(onSubmit)} autoComplete="off">
                <div className="mb-4">
                    <input
                        {...register("nome", {
                            required: 'Campo obrigatório'
                        })}
                        type="text"
                        className={`form-control base-input ${errors.nome ? 'is-invalid' : ''}`}
                        placeholder="Nome completo"
                        name="nome"
                    />
                    <div className="invalid-feedback d-block">{errors.nome?.message}</div>
                </div>
                <div className="mb-2">
                    <input
                        {...register("email", {
                            required: 'Campo obrigatório',
                            pattern: {
                                value: /^[A-Z0-9._%+-]+@aluno.ifsc.edu.br/i,
                                message: 'E-mail inválido (utilize @aluno.ifsc.edu.br)'
                            }
                        })}
                        type="text"
                        className={`form-control base-input ${errors.email ? 'is-invalid' : ''}`}
                        placeholder="E-mail institucional (@aluno.ifsc.edu.br)"
                        name="email"
                        onChange={handleKeyPress}
                        autoComplete="off"
                    />
                    <div className="invalid-feedback d-block">{errors.email?.message}</div>
                </div>

                <div className="mb-2 input-group">
                    <input
                        {...register("senha", {
                            required: 'Campo obrigatório'
                        })}
                        type={showPassword ? "text" : "password"}
                        className={`form-control base-input ${errors.senha ? 'is-invalid' : ''}`}
                        placeholder="Senha de acesso"
                        name="senha"
                        autoComplete="off"
                        onKeyPress={handleKeySenha}
                    />
                    <a href="#" onClick={handlePasswordClick}>
                        <button className="input-group-text bg-dark text-light form-control base-input visao-senha">
                            {showPassword ? <VisibilityIcon/> : <VisibilityOffIcon/>}
                        </button>
                    </a>
                    <div className="invalid-feedback d-block">{errors.senha?.message}</div>
                </div>

                {hasRequestError &&
                    <div className="auth-error">
                        <p>Erro ao realizar cadastro. Por favor, confira se o e-mail já está sendo utilizado.</p>
                    </div>
                }

                <div className="login-submit">
                    <ButtonIcon text="Enviar"/>
                </div>
            </form>
        </div>
    );
};

export default NovoEstudante;