import {ReactComponent as ArrowIcon} from 'assets/images/arrow.svg';
import {Link, useHistory} from 'react-router-dom';
import ButtonIcon from 'components/ButtonIcon';

import './styles.css';
import {useState} from "react";
import logoIfsc from "../../../assets/images/ifsc-logo.png";

const Register = () => {

    const [selectedRadioBtn, setSelectedRadioBtn] = useState('estudante');
    const isRadioSelected = (value: string): boolean => selectedRadioBtn === value;
    const handleRadioClick = (e: React.ChangeEvent<HTMLInputElement>): void => setSelectedRadioBtn(e.currentTarget.value);

    const history = useHistory();

    const handleNextClick = (event: React.MouseEvent<HTMLAnchorElement>) => {
        event.preventDefault();
        if (selectedRadioBtn === 'estudante') {
            history.push('/auth/register/student');
        } else {
            history.push('/auth/register/company');
        }
    }

    return (
        <div className="base-card login-card">
            <div className="logo-mobile">
                <img src={logoIfsc} alt="Logo do IFSC" className="img-card"/>
                <h3>IFSC VAGAS</h3>
                <h4>Câmpus Tubarão</h4>
            </div>
            <Link to="/auth">
                <div className="goback-container">
                    <ArrowIcon/>
                    <h2>VOLTAR</h2>
                </div>
            </Link>
            <h1>SOLICITAR CADASTRO</h1>


            <div className="type-choices">
                <h2>Você é...</h2>

                <div className="radio-buttons">
                    <div className="radio-buttons">

                        <label className="custom-radio">
                            <input type="radio" name="radio" value='estudante' checked={isRadioSelected('estudante')} onChange={handleRadioClick}/>
                            <span className="radio-btn"
                            ><i className="las la-check"></i>
                            <div className="hobbies-icon">
                              <i className="las la-user-graduate"></i>
                              <h3>Estudante</h3>
                            </div>
                          </span>
                        </label>

                        <label className="custom-radio">
                            <input type="radio" name="radio" value='empresa' checked={isRadioSelected('empresa')} onChange={handleRadioClick}/>
                            <span className="radio-btn"
                            ><i className="las la-check"></i>
                            <div className="hobbies-icon">
                              <i className="las la-building"></i>
                              <h3>Empresa</h3>
                            </div>
                          </span>
                        </label>

                    </div>
                </div>
            </div>

            <div className="login-submit">
                <a href="#" onClick={handleNextClick}>
                    <ButtonIcon text="Avançar"/>
                </a>
            </div>

        </div>
    );
};

export default Register;
