import './styles.css';
import {useCallback, useContext, useEffect, useState} from "react";
import {requestBackend, requestBackendPendentes} from "../../util/requests";
import {SpringPage} from "../../types/spring";
import {AxiosRequestConfig} from "axios";
import Pagination from "../../components/Pagination";
import {SolicitacoesPendentesContext} from "../../SolicitacoesPendentesContext";
import {Link} from 'react-router-dom';
import UserFilter, {UserFilterData} from "./UserFilter";
import CardUsuario from "./CardUsuario";
import {UsuarioDTO} from "../../types/papel";

type ControlComponentsData = {
    activePage: number;
    filterData: UserFilterData;
}

const Usuarios = () => {

    const [page, setPage] = useState<SpringPage<UsuarioDTO>>();

    const [controlComponentsData, setControlComponentsData] = useState<ControlComponentsData>(
        {
            activePage: 0,
            filterData: {email: "", papel: null}
        }
    );

    const handlePageChange = (pageNumber: number) => {
        setControlComponentsData({activePage: pageNumber, filterData: controlComponentsData.filterData});
    };

    const handleSubmitFilter = (data: UserFilterData) => {
        setControlComponentsData({activePage: 0, filterData: data});
    }

    const getPendencias = useCallback(() => {
        const config: AxiosRequestConfig = {
            method: 'GET',
            url: '/usuarios',
            withCredentials: true,
            params: {
                page: controlComponentsData.activePage,
                size: 3,
                email: controlComponentsData.filterData.email,
                papelId: controlComponentsData.filterData.papel?.id
            }
        };

        requestBackend(config)
            .then(response => {
                    setPage(response.data);
                }
            );
    }, [controlComponentsData]);

    useEffect(() => {
        getPendencias();
    }, [getPendencias]);


    return (
        <div className="product-crud-container">
            <div className="product-crud-bar-container">
                <Link to="/app/novo-usuario">
                    <button className="btn btn-primary text-white btn-crud-add">
                        ADICIONAR
                    </button>
                </Link>

                <UserFilter onSubmitFilter={handleSubmitFilter}/>
            </div>
            <div className="row">
                {
                    page?.content.map(solic => (
                        <div key={solic.id} className="col-sm-6 col-md-12">
                            <CardUsuario usuario={solic}/>
                        </div>
                    ))
                }
            </div>
            {
                page && page?.totalElements > 0 &&
                <Pagination forcePage={page?.number} pageCount={(page) ? page.totalPages : 0} range={3}
                            onChange={handlePageChange}/>
            }
            {
                page && page?.totalElements === 0 &&
                <h3>Não foram encontrados usuários no sistema com o filtro selecionado.</h3>
            }
        </div>
    );
}

export default Usuarios;
