import './styles.css';
import {Link} from "react-router-dom";
import ButtonIcon from "../../../components/ButtonIcon";
import {ReactComponent as MainImage} from "../../../assets/images/main-image.svg";
import {getAuthData} from "../../../util/storage";
import {hasAnyRoles} from "../../../util/auth";

const Apresentacao = () => {

    return (
        <div className="home-container">
            <div className="base-card home-card">
                <div className="home-content-container">
                    <div>
                        <h1>Olá, {getAuthData().nome.replace(/ .*/,'')}</h1>

                        { hasAnyRoles(["ROLE_ADMIN"]) &&
                            <p>Aqui você pode gerenciar o sistema de vagas do seu Câmpus.</p>
                        }

                        { hasAnyRoles(["ROLE_EMPRESA"]) &&
                            <p>Aqui você pode gerenciar suas vagas e as candidaturas.</p>
                        }

                        { hasAnyRoles(["ROLE_ESTUDANTE"]) &&
                            <p>Aqui você pode encontrar a sua próxima vaga no mercado de trabalho.</p>
                        }
                    </div>
                    <div>
                        { hasAnyRoles(["ROLE_ADMIN"]) &&
                            <Link to="/app/usuarios">
                                <ButtonIcon text="Visualizar usuários cadastrados"/>
                            </Link>
                        }
                        { hasAnyRoles(["ROLE_EMPRESA"]) &&
                            <Link to="/app/vagas-empresa">
                                <ButtonIcon text="Visualizar vagas cadastradas"/>
                            </Link>
                        }
                        { hasAnyRoles(["ROLE_ESTUDANTE"]) &&
                            <Link to="/app/vagas">
                                <ButtonIcon text="Visualizar vagas"/>
                            </Link>
                        }
                    </div>
                </div>
                <div className="home-image-container">
                    <MainImage/>
                </div>
            </div>
        </div>
    );
}

export default Apresentacao;