import './styles.css';
import {useCallback, useEffect, useState} from "react";
import {requestBackend} from "../../util/requests";
import {SpringPage} from "../../types/spring";
import {AxiosRequestConfig} from "axios";
import Pagination from "../../components/Pagination";
import {CandidaturaDTO} from "../../types/papel";
import {useLocation} from "react-router-dom";
import CardFiltroCandidaturasAdmin from "./CardFiltroCandidaturasAdmin";
import CardCandidaturaAdmin from "./CardCandidaturaAdmin";

type ControlComponentsData = {
    activePage: number;
}

type VagaId = {
    vagaId: string;
}

const CandidaturasAdmin = () => {

    const vagaId = useLocation<VagaId>().state?.vagaId || 0;

    const [page, setPage] = useState<SpringPage<CandidaturaDTO>>();

    const [controlComponentsData, setControlComponentsData] = useState<ControlComponentsData>(
        {
            activePage: 0
        }
    );

    const handlePageChange = (pageNumber: number) => {
        setControlComponentsData({
            activePage: pageNumber
        });
    };

    // const handleSubmitFilter = (data: VagasEmpresaFilterData) => {
    //     setControlComponentsData({activePage: 0, filterData: data});
    // }

    const getCandidaturas = useCallback(() => {
        const config: AxiosRequestConfig = {
            method: 'GET',
            url: '/candidaturas',
            withCredentials: true,
            params: {
                page: controlComponentsData.activePage,
                size: vagaId > 0 ? 2 : 3,
                vagaId: vagaId
                // titulo: controlComponentsData.filterData.titulo,
                // tipoId: controlComponentsData.filterData.tipoVaga?.id
            }
        };

        requestBackend(config)
            .then(response => {
                    setPage(response.data);
                }
            );
    }, [controlComponentsData, vagaId]);

    useEffect(() => {
        getCandidaturas();
    }, [getCandidaturas]);


    return (
        <div className="product-crud-container">
            {/*<div className="product-crud-bar-container">*/}
            {/*    <Link to="/app/vaga-empresa/nova">*/}
            {/*        <button className="btn btn-primary text-white btn-crud-add">*/}
            {/*            ADICIONAR*/}
            {/*        </button>*/}
            {/*    </Link>*/}

            {/*    <VagasEmpresaFilter onSubmitFilter={handleSubmitFilter}/>*/}
            {/*</div>*/}
            <div className="row">
                {vagaId > 0 &&
                    <div className="col-sm-6 col-md-12">
                        <CardFiltroCandidaturasAdmin/>
                    </div>
                }
                {
                    page?.content.map(candidatura => (
                        <div key={candidatura.id} className="col-sm-6 col-md-12">
                            {/* eslint-disable-next-line react/jsx-no-undef */}
                            <CardCandidaturaAdmin candidatura={candidatura}/>
                        </div>
                    ))
                }
            </div>
            {
                page && page?.totalElements > 0 &&
                <Pagination forcePage={page?.number} pageCount={(page) ? page.totalPages : 0} range={3}
                            onChange={handlePageChange}/>
            }
            {
                page && page?.totalElements === 0 &&
                <h3>Nenhuma candidatura encontrada.</h3>
            }
        </div>
    );
}

export default CandidaturasAdmin;
