import 'react-toastify/dist/ReactToastify.css';
import './assets/styles/custom.scss';
import './App.css';
import Routes from "./Routes";
import {useEffect, useState} from "react";
import {AuthContext, AuthContextData} from "./AuthContext";
import { ToastContainer } from 'react-toastify';
import {SolicitacoesPendentesContext} from "./SolicitacoesPendentesContext";
import {VisualizacaoCandidaturasPendentesContext} from "./VisualizacaoCandidaturasPendentesContext";
import axios from "axios";
import {BASE_URL} from "./util/requests";

function App() {
    const [isLoading, setIsLoading] = useState(true);

    const [solicitacoesPendentes, setSolicitacoesPendentes] = useState<number>(0);
    const [visualizacaoCandidaturasPendentes, setVisualizacaoCandidaturasPendentes] = useState<number>(0);

    const [authContextData, setAuthContextData] = useState<AuthContextData>({
        authenticated: false
    });

    function timeout(delay: number) {
        return new Promise( res => setTimeout(res, delay) );
    }

    useEffect(() => {
        if (isLoading) {
            axios.get(`${BASE_URL}/actuator/health`)
                .then(response => {
                    if (response.data.status === 'UP') {
                        setIsLoading(false)
                    } else {
                        window.location.reload();
                    }
                })
                .catch(error => {
                    window.location.reload();
                });

            // if(isLoading){
            //     timeout(1000);
            // }
            console.log('tentando...')
        }
    });

    if (!isLoading) {
        return(
        <AuthContext.Provider value={{authContextData, setAuthContextData}}>
            <VisualizacaoCandidaturasPendentesContext.Provider
                value={{visualizacaoCandidaturasPendentes, setVisualizacaoCandidaturasPendentes}}>
                <SolicitacoesPendentesContext.Provider value={{solicitacoesPendentes, setSolicitacoesPendentes}}>
                    <Routes/>
                    <ToastContainer/>
                </SolicitacoesPendentesContext.Provider>
            </VisualizacaoCandidaturasPendentesContext.Provider>
        </AuthContext.Provider>
        );
    } else {
        return <h1>Carregando servidores, por favor, aguarde...</h1>
    }
}

export default App;
