import {ReactComponent as ArrowIcon} from 'assets/images/arrow.svg';
import {Link, useParams} from 'react-router-dom';
import './styles.css';
import {useEffect, useState} from "react";
import {requestAtivarUsuario, requestBackend, requestInativarUsuario} from "../../util/requests";
import {UsuarioDTO} from "../../types/papel";
import {toast} from "react-toastify";
import {getTokenData} from "../../util/auth";


type UrlParams = {
    usuarioId: string;
}

const DetalhesUsuario = () => {

    const {usuarioId} = useParams<UrlParams>();

    const [usuario, setUsuario] = useState<UsuarioDTO>();

    const [papel, setpapel] = useState<string>();

    useEffect(() => {
        requestBackend({url: `/usuarios/${usuarioId}`, withCredentials: true})
            .then(response => {
                setUsuario(response.data)
            })
    }, [usuarioId]);

    useEffect(() => {
        if (usuario?.papeis[0].autoridade === 'ROLE_ADMIN') {
            setpapel('Administrador')
        } else if (usuario?.papeis[0].autoridade === 'ROLE_EMPRESA') {
            setpapel('Empresa')
        } else if (usuario?.papeis[0].autoridade === 'ROLE_ESTUDANTE') {
            setpapel('Estudante')
        }
    }, [usuario])

    const handleInativarClick = (event: React.MouseEvent<HTMLAnchorElement>) => {
        event.preventDefault();

        requestInativarUsuario(usuario?.id)
            .then(response => {
                toast.warn(`Usuário ${usuario?.email} inativo`);
                requestBackend({url: `/usuarios/${usuarioId}`, withCredentials: true})
                    .then(response => {
                        setUsuario(response.data)
                    });
            }).catch(() => {
            toast.error("Erro ao inativar o usuário");
        })
    }

    const handleAtivarClick = (event: React.MouseEvent<HTMLAnchorElement>) => {
        event.preventDefault();

        requestAtivarUsuario(usuario?.id)
            .then(response => {
                toast.success(`Usuário ${usuario?.email} ativo`);
                requestBackend({url: `/usuarios/${usuarioId}`, withCredentials: true})
                    .then(response => {
                        setUsuario(response.data)
                    });
            }).catch(() => {
            toast.error("Erro ao ativar o usuário");
        })
    }

    return (
        <div className="product-details-container">
            <div className="base-card product-details-card">
                <Link to="/app/usuarios">
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR</h2>
                    </div>
                </Link>
                <div className="row">

                    <div className="col-xl-6">
                        <div className="description-container">
                            <h2>Descrição do usuário</h2>
                            <p>Tipo: {papel}</p>
                            <p>Nome: {usuario?.nome}</p>
                            <p>Email: {usuario?.email}</p>
                            {usuario?.nomeEmpresa &&
                                <p>Empresa: {usuario?.nomeEmpresa}</p>
                            }
                            {usuario?.ativo && <div className="frase">
                                <p>
                                    Status:
                                    <text className="title"> Ativo</text></p>
                            </div>}
                            {!usuario?.ativo && <div className="frase">
                                <p>
                                    Status:
                                    <text className="text-danger"> Inativo</text></p>
                            </div>}
                        </div>
                    </div>

                    <div className="col-xl-6">
                        {
                            usuario?.ativo &&
                            getTokenData()?.user_name !== usuario.email &&
                            <>
                                <a href="#" onClick={handleInativarClick}>
                                    <button
                                        className="btn-outline-danger product-crud-card-button product-crud-card-button-first">
                                        INATIVAR
                                    </button>
                                </a>
                                <br/>
                            </>
                        }
                        {
                            !usuario?.ativo &&
                            getTokenData()?.user_name !== usuario?.email &&
                            <>
                                <a href="#" onClick={handleAtivarClick}>
                                    <button className="btn-outline-primary product-crud-card-button">
                                        ATIVAR
                                    </button>
                                </a>
                                <br/>
                            </>
                        }
                        <Link to={`/app/nova-senha/${usuario?.id}`}>
                            <button className="btn-outline-secondary product-crud-card-button">
                                NOVA SENHA
                            </button>
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default DetalhesUsuario;