import {createContext} from "react";

export type SolicitacoesPendentesContextType = {
    solicitacoesPendentes: number;
    setSolicitacoesPendentes: (solicitacoesPendentes: number) => void;
}

export const SolicitacoesPendentesContext = createContext<SolicitacoesPendentesContextType>({
    solicitacoesPendentes:0,
    setSolicitacoesPendentes: () => null
});