import './styles.css';
import {Link, useHistory} from "react-router-dom";
import {ReactComponent as ArrowIcon} from "../../assets/images/arrow.svg";
import {useForm} from "react-hook-form";
import {useState} from "react";
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import {requestNovoEstudanteManual, requestRedefinirSenha} from "../../util/requests";
import ButtonIcon from "../../components/ButtonIcon";
import {toast} from "react-toastify";

type FormData = {
    senhaAtual: string;
    senhaNova: string;
}

const RedefinirSenha = () => {

    const history = useHistory();

    const {register, handleSubmit, formState: {errors}} = useForm<FormData>();

    const [hasRequestError, setHasRequestError] = useState(false);

    const [showPasswordAntiga, setShowPasswordAntiga] = useState(false);
    const [showPasswordNova, setShowPasswordNova] = useState(false);

    const handlePasswordAntigaClick = (event: React.MouseEvent<HTMLAnchorElement>) => {
        event.preventDefault();
        setShowPasswordAntiga(!showPasswordAntiga);
    }

    const handlePasswordNovalick = (event: React.MouseEvent<HTMLAnchorElement>) => {
        event.preventDefault();
        setShowPasswordNova(!showPasswordNova);
    }

    const onSubmit = (formData: FormData) => {
        requestRedefinirSenha(formData)
            .then(response => {
                history.push('/app/home')
                toast.success(`Senha alterada com sucesso`);
            })
            .catch(error => {
                setHasRequestError(true);
            });
    };

    const handleKeyPress = () => {
        setHasRequestError(false)
    }

    const handleKeySenha = (event: any) => {
        if (event.key === 'Enter') {
            event.preventDefault();
            const teste = document.getElementById('enviar');
            if (teste != null) {
                teste.click();
            }
        }
    }

    return (
        <div className="base-card login-card geral">
            <Link to="/app/home">
                <div className="goback-container">
                    <ArrowIcon/>
                    <h2>CANCELAR</h2>
                </div>
            </Link>

            <h1>ALTERAR SENHA</h1>

            <form onSubmit={handleSubmit(onSubmit)} autoComplete="off">
                {/*--------------------------------------------------------------------------------------------------------------------------------------------------------------*/}
                <div className="mb-2 input-group">
                    <input
                        {...register("senhaAtual", {
                            required: 'Campo obrigatório'
                        })}
                        type={showPasswordAntiga ? "text" : "password"}
                        className={`form-control base-input ${errors.senhaAtual ? 'is-invalid' : ''}`}
                        placeholder="Senha atual"
                        name="senhaAtual"
                        autoComplete="off"
                        onChange={handleKeyPress}
                        onKeyPress={handleKeySenha}
                    />
                    <a href="#" onClick={handlePasswordAntigaClick}>
                        <button className="input-group-text bg-dark text-light form-control base-input visao-senha">
                            {showPasswordAntiga ? <VisibilityIcon/> : <VisibilityOffIcon/>}
                        </button>
                    </a>
                    <div className="invalid-feedback d-block">{errors.senhaAtual?.message}</div>
                </div>
                {/*--------------------------------------------------------------------------------------------------------------------------------------------------------------*/}
                <div className="mb-2 input-group">
                    <input
                        {...register("senhaNova", {
                            required: 'Campo obrigatório'
                        })}
                        type={showPasswordNova ? "text" : "password"}
                        className={`form-control base-input ${errors.senhaNova ? 'is-invalid' : ''}`}
                        placeholder="Nova senha"
                        name="senhaNova"
                        autoComplete="off"
                        onKeyPress={handleKeySenha}
                    />
                    <a href="#" onClick={handlePasswordNovalick}>
                        <button className="input-group-text bg-dark text-light form-control base-input visao-senha">
                            {showPasswordNova ? <VisibilityIcon/> : <VisibilityOffIcon/>}
                        </button>
                    </a>
                    <div className="invalid-feedback d-block">{errors.senhaNova?.message}</div>
                </div>
                {/*--------------------------------------------------------------------------------------------------------------------------------------------------------------*/}
                {hasRequestError &&
                    <div className="auth-error">
                        <p>Erro ao alterar a senha. Por favor, confira sua senha atual.</p>
                    </div>
                }
                {/*--------------------------------------------------------------------------------------------------------------------------------------------------------------*/}
                <div className="login-submit">
                    <ButtonIcon text="Alterar"/>
                </div>
            </form>
        </div>
    );
};

export default RedefinirSenha;