import {ReactComponent as ArrowIcon} from 'assets/images/arrow.svg';
import {Link} from 'react-router-dom';
import './styles.css';


const SucessoCandidaturaEstudante = () => {

    return (
        <div className="product-details-container">

            <div className="base-card product-details-card all">
                <Link to={`/app/vagas`}>
                    <div className="goback-container">
                        <ArrowIcon/>
                        <h2>VOLTAR PARA VAGAS</h2>
                    </div>
                </Link>
                <div className="row">

                    <div className="col-xl-12">

                        <h4>
                            Parabéns, seu currículo foi enviado com sucesso para a vaga escolhida!
                            Por favor, aguarde o contato da empresa.
                        </h4>
                        <br/>
                        <h3>
                            Boa sorte!
                        </h3>

                    </div>
                </div>
            </div>
        </div>
    );
}

export default SucessoCandidaturaEstudante;